function action_validateSuccess() {
    return {
        type:'validateSuccess',
        payload:{}
    }
}

function action_validateFail() {
    return {
        type:'validateFail',
        payload:{}
    }
}


function action_setAccount(F_ACCOUNT_NO,F_PASSWORD) {
    return {
        type:'setAccount',
        payload:{F_ACCOUNT_NO,F_PASSWORD}
    }
}

function action_setAccountNo(F_ACCOUNT_NO) {
    return {
        type:'setAccountNo',
        payload:F_ACCOUNT_NO
    }
}

function action_setPassword(F_PASSWORD) {
    return {
        type:'setPassword',
        payload:F_PASSWORD
    }
}

function action_setCompanyID(F_COMPANY_ID) {
    return {
        type:'setCompanyID',
        payload:F_COMPANY_ID
    }
}

function action_logout() {
    return {
        type:'logout',
        payload:{}
    }
}

export {action_validateSuccess,action_validateFail,action_setAccount,action_setAccountNo,action_setPassword,action_logout,action_setCompanyID}
