import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';


export default class Voltage extends React.Component
{
    constructor(props)
    {
        super(props);
        this.getVoltages=(MachineryType)=>
        {
            return [
                {value:1,text:'380'},
                {value:2,text:'660'},
                {value:3,text:'其它'},

            ];

        }
        this.state={
            Voltages:this.getVoltages(props.machineryType)
        }



    }

    render() {
        return (

            <FormControl variant="outlined" fullWidth>
                <InputLabel htmlFor="outlined-Voltage-simple">
                    电压类型
                </InputLabel>
                <Select
                    labelWidth={80}
                    value={this.state.Voltages[0].value}
                    onChange={this.handleVoltageChange}
                    input={<OutlinedInput name="Voltage" id="outlined-Voltage-simple" />}
                >


                    {
                        this.state.Voltages.map((item)=>{
                            return (<MenuItem value={item.value}>{item.text}</MenuItem>)
                        })
                    }

                </Select>
            </FormControl>
        )

    }
}
