import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {ListItemText,ListItemIcon,MenuList,InputAdornment,TextField ,FormControlLabel,Switch,Tooltip ,GridListTileBar,GridList,GridListTile,Grid,Divider,Chip,Container,Menu,MenuItem,Fab,IconButton,Box,AppBar,Toolbar,Typography,Button,InputBase,Badge} from '@material-ui/core';

import {AccountCircle as AccountCircleIcon ,Home as HomeIcon,Face as FaceIcon ,AccessAlarm,Menu as MenuIcon,Search as SearchIcon, Notifications  as NotificationsIcon, Add as AddIcon } from '@material-ui/icons';


import Carousel0 from '../images/Carousel0.jpg'
import Carousel1 from '../images/Carousel1.jpg'
import Carousel2 from '../images/Carousel2.jpg'
import Carousel3 from '../images/Carousel3.jpg'


export default  class MaterialUIGridList extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            error:false,
            disabled:false,
            required:false,
            helpText:'Please select your name',
            type:'search',
            password:false,
            fullWidth:false,
            anchor:null,

        }

        this.handleTextFieldPasswordClick =(e)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    password : !prevState.password,
                    type:(!prevState.password)? ('password'):('search'),
                }
            })
        }
        this.handleNameChange=(event)=>{

            let name =event.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    name
                }
            })

        }
        this.handleTextFieldErrorClick =(e)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    error : !prevState.error,
                    helpText: (!prevState.error) ? ('something is wrong') : ('please input your name')
                }
            })
        }
        this.handleTextFieldDisabledClick =(e)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    disabled : !prevState.disabled
                }
            })
        }
        this.handleTextFieldRequiredClick =(e)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    required: !prevState.required
                }
            })
        }
        this.handleTextFieldFullWidthClick =(e)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    fullWidth: !prevState.fullWidth
                }
            })
        }
        this.handleIconButtonClick=(e)=>{


        }

        this.handleOpenMenu=(event)=>{
            let anchor = event.currentTarget;

            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    anchor,
                }
            })


        }

        this.handleCloseMenu=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    anchor:null,
                }
            })

        }
    }

    render() {

        let tiles=[
            {key:1,src:Carousel0,cols:3},
            {key:2,src:Carousel1,cols:3},
            {key:3,src:Carousel2,cols:3},
            {key:4,src:Carousel3,cols:3},
            {key:5,src:Carousel0,cols:3},
            {key:6,src:Carousel1,cols:3},
            {key:7,src:Carousel2,cols:3},
            {key:8,src:Carousel3,cols:3},
        ]

        return (
            <Container maxWidth={'sm'} style={{height:'200vh'}}>
            <GridList style={{width:600,height:250,flexWrap:'wrap'}} cols={6} cellHeight={120} >
                <GridListTile cols={6} style={{height:'auto',backgroundColor:'rgba(0,0,0,0.7)'}}>
                    <Grid container>
                        <Grid item sm={1} style={{padding:10,margin:0}}>

                                <HomeIcon style={{fontSize:30,color:'white'}}/>


                        </Grid>
                        <Grid item sm={2}>
                            <Typography variant={'h6'} style={{padding:10,margin:0,color:'white'}} >
                                Graphis
                            </Typography>

                        </Grid>

                    </Grid>

                </GridListTile>
                {tiles.map((tile,index)=>{
                    return (
                        <GridListTile cols={tile.cols} key ={tile.key} style={{height:150}}>
                            <img src={tile.src} />
                            <GridListTileBar title={<span style={{padding:10,display:'inline-block'}}>Grphis</span>}  actionIcon={
                                <IconButton onClick={this.handleIconButtonClick}>
                                    <HomeIcon style={{color:'white'}}/>
                                </IconButton>
                            }></GridListTileBar>
                        </GridListTile>


                        )

                })}



                <Divider/>

            </GridList>

                <Grid container style={{marginTop:30}}>
                    <Grid item sm={3}>
                        <FormControlLabel control={<Switch value={'textFieldPassword'} checked={this.state.password} onClick={this.handleTextFieldPasswordClick}/>}
                                          label={<span style={{fontSize:20}}>Password</span>}
                                          labelPlacement={'top'}
                        >

                        </FormControlLabel>

                    </Grid>
                    <Grid item sm={3}>
                        <FormControlLabel control={<Switch color={'primary'} value={'textFieldError'} checked={this.state.error} onClick={this.handleTextFieldErrorClick}/>}
                                          label={<span style={{fontSize:20}}>Error</span>}
                                        labelPlacement={'top'}
                        >


                        </FormControlLabel>

                    </Grid>
                    <Grid item sm={3}>
                        <FormControlLabel control={<Switch color={'primary'} value={'textFieldDisabled'} checked={this.state.disabled} onClick={this.handleTextFieldDisabledClick}/>}
                                          label={<span style={{fontSize:20}}>Disabled</span>}
                                          labelPlacement={'top'}
                        >


                        </FormControlLabel>

                    </Grid>
                    <Grid item sm={3}>
                        <FormControlLabel control={<Switch color={'primary'} value={'textFieldRequired'} checked={this.state.required} onClick={this.handleTextFieldRequiredClick}/>}
                                          label={<span style={{fontSize:20}}>Required</span>}
                                          labelPlacement={'top'}
                        >


                        </FormControlLabel>

                    </Grid>
                    <Grid item sm={3}>
                        <FormControlLabel control={<Switch color={'primary'} value={'textFieldFullWidth'} checked={this.state.fullWidth} onClick={this.handleTextFieldFullWidthClick}/>}
                                          label={<span style={{fontSize:20}}>FullWidth</span>}
                                          labelPlacement={'top'}
                        >


                        </FormControlLabel>

                    </Grid>
                </Grid>

                <Grid container style={{marginTop:30}}>
                    <Grid item sm={12}>
                        <TextField label={'Name'} value = {this.state.name} onChange ={this.handleNameChange}
                                   type={this.state.type}
                                   error={this.state.error}
                                   required={this.state.required}
                                   disabled={this.state.disabled}
                                   helperText={this.state.helpText}
                                   fullWidth = {this.state.fullWidth}
                                   variant="outlined"
                                   InputProps={{
                                       //shrink: true,
                                       startAdornment: (
                                           <InputAdornment position="start">
                                               <AccountCircleIcon />
                                           </InputAdornment>
                                       ),
                                   }}
                        >


                        </TextField>

                    </Grid>
                    <Grid item sm={12}>


                    </Grid>
                </Grid>

                <IconButton  aria-controls="simple-menu" aria-haspopup="true" onClick={this.handleOpenMenu} color={'secondary'} variant={'contained'}>
                    <MenuIcon/>
                </IconButton>


                <Menu
                    id="simple-menu"
                    anchorEl={this.state.anchor}
                    keepMounted
                    open={Boolean(this.state.anchor)}
                    onClose={this.handleCloseMenu}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: -10,
                        horizontal: 'left',
                    }}

                    PaperProps={{
                        style: {
                            width: 300,
                        },
                    }}

                >


                            <MenuItem onClick={this.handleCloseMenu} >
                                <ListItemIcon><HomeIcon color={'primary'}/></ListItemIcon>
                                <ListItemText primary="Profile" style={{color:'#2962ff'}}/>

                                </MenuItem>
                            <MenuItem onClick={this.handleCloseMenu}>

                                <ListItemIcon><FaceIcon/></ListItemIcon>
                                <ListItemText primary="Account" />
                            </MenuItem>

                    <MenuItem onClick={this.handleCloseMenu} >
                        <ListItemIcon><HomeIcon color={'primary'}/></ListItemIcon>
                        <ListItemText primary="Profile" style={{color:'#2962ff'}}/>

                    </MenuItem>
                    <MenuItem onClick={this.handleCloseMenu}>

                        <ListItemIcon><FaceIcon/></ListItemIcon>
                        <ListItemText primary="Account" />
                    </MenuItem>                            <MenuItem onClick={this.handleCloseMenu}  >
                    <ListItemIcon><HomeIcon color={'primary'}/></ListItemIcon>
                    <ListItemText primary="Profile" style={{color:'#2962ff'}}/>

                </MenuItem>
                    <MenuItem onClick={this.handleCloseMenu}>

                        <ListItemIcon><FaceIcon/></ListItemIcon>
                        <ListItemText primary="Account" />
                    </MenuItem>

                </Menu>


            </Container>
        )
    }

}
