
import React from 'react'

import { Drawer, Button ,List,Icon,Avatar,Badge,Tooltip,Divider,Statistic,Popover,Descriptions  } from 'antd';


import '../css/drawerDemo.css'

export default class DrawerDemo extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = { visible: false ,
            data:[
                <div className={'listItem'}><Icon type="github" style={{padding:10,fontSize:20}}/> <span style={{paddingRight:10,fontSize:20,fontFamily:'georgia'}}>Github</span></div>,
                <div className={'listItem'}><Icon type="google" style={{padding:10,fontSize:20}}/> <span style={{paddingRight:10,fontSize:20,fontFamily:'georgia'}}>Google</span></div>,
                <div className={'listItem'}><Icon type="twitter" style={{padding:10,fontSize:20}}/> <span style={{paddingRight:10,fontSize:20,fontFamily:'georgia'}}>Twitter</span></div>,
            ]
        };

        this.showDrawer = () => {
            this.setState({
                visible: true,
            });
        };

        this.closeDrawer  = () => {
            this.setState({
                visible: false,
            });
        };

        this.OpenMenu=(e)=>{

        }

    }

    render() {

        let list =                <List

            style={{margin:0,padding:0}}
            bordered = {false}
            dataSource={this.state.data}
            renderItem={item => <List.Item>{item}</List.Item>}
        />

        return (<div style={{height:'100vh'}}>
            <Descriptions title={<Badge count={1}><Avatar size={40} icon='user' style={{backgroundColor:'orange'}} shape="square" /> </Badge>} bordered>
                <Descriptions.Item label={
                    <div style={{fontSize:20}}>
                        <Icon type={"github"}/>
                        <span style={{padding:10,fontFamily:'georgia'}}>Github</span>
                    </div>

                }>
                    <span style={{padding:10,fontFamily:'georgia',fontSize:20}}>Zhou Maomao</span>

                </Descriptions.Item>
                <Descriptions.Item label={<Icon type={"twitter"}/>}>Zhou Maomao</Descriptions.Item>
                <Descriptions.Item label={<Icon type={"github"}/>}>Zhou Maomao</Descriptions.Item>
                <Descriptions.Item label={<Icon type={"twitter"}/>} span={2}>Zhou Maomao</Descriptions.Item>
                <Descriptions.Item label={<Icon type={"twitter"}/>}>Zhou Maomao</Descriptions.Item>
            </Descriptions>
            <Divider/>
            <Popover content={list} title="">
                <Button type="primary">Hover me</Button>
            </Popover>

            <Divider/>
            <Statistic title="Active Users" value={112893.12} prefix={<Icon type="arrow-down" />}/>
            <Divider/>
            <Tooltip title="托管平台">
                <Icon type="github" style={{fontSize:26,margin:10}}/>

            </Tooltip>
            <Divider/>
            <Button type="primary" onClick={this.showDrawer} style={{fontFamily:'georgia'}}>
                Open drawer
            </Button>
            <Drawer
                title={<Badge count={1}><Avatar size={40} icon='user' style={{backgroundColor:'orange'}} shape="square" /> </Badge>}
                placement="left"
                closable={false}
                onClose={this.closeDrawer}
                visible={this.state.visible}
            >
                <List
                    style={{margin:0,padding:0}}
                    bordered = {false}
                    dataSource={this.state.data}
                    renderItem={item => <List.Item>{item}</List.Item>}
                />
            </Drawer>
            <Divider/>



        </div>)

    }
}


