


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import {Search as SearchIcon ,Feedback as FeedbackIcon ,EvStation as EvStationIcon ,EventSharp as EventSharpIcon ,Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";


class LvEfficiencyData extends React.Component {

    getOption(){
        return {
            //color: ['#003366', '#006699', '#4cabce', '#e5323e'],
            //color: ['red', 'green', 'orange', '#C0C0C0'],
            color: ['#e64a19', '#388e3c', '#f57c00', '#fbc02d', '#e5323e'],
            title: {
                x: '20px',
                y: '20px',
                text: '能效等级份额(万千瓦)',
                subtext : "Analysis: Efficiency share"
            },
            tooltip: {
                show: true,
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"

            },
            //阴影的配置
            itemStyle: {
                normal: {
                    //扇形的颜色
                    //color: '#c23531',
                    shadowBlur: 50,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                emphasis: {
                    shadowBlur: 200,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            },
            legend: {
                bottom: 0,
                left: 'center',
                data: ['IE2','IE3','IE4','其它'],
                selected: {},
            },
            xAxis: [

            ],
            yAxis: [

            ],
            series: [
                {
                    "name": "能耗",
                    "type": "pie",//bar line
                    avoidLabelOverlap: false,
                    "data": [
                        {value:310, name:'IE2'},
                        {value:335, name:'IE3'},
                        {value:235, name:'IE4'},
                        {value:534, name:'其它'},
                    ],
                    radius: ['39%', '70%'],
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    label: {            //饼图图形上的文本标签
                        normal: {

                            backgroundColor: "transparent",            //标题背景色
                            //提示框浮层内容格式器，支持字符串模板和回调函数两种形式,
                            //模板变量有 {a}, {b}，{c}，{d}，{e}，分别表示系列名，数据名，数据值等
                            formatter: '{b}\n{d}%',
                            show: false,
                            position: 'center'

                        },
                        emphasis: {
                            show: true,
                            textStyle: {
                                fontSize: '30',
                                fontWeight: 'bold'
                            }
                        }
                    },
                }

            ]
        }
    }

    constructor(props)
    {
        super(props)
        this.state={
            option: this.getOption(),
            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            quarters:[{value:1,text:'一季度'},{value:2,text:'二季度'},{value:3,text:'三季度'},{value:4,text:'四季度'}],
            quarter:1,

            companies:[{value:'01',text:'江苏大中'},{value:'02',text:'卧龙电驱'}],
            company:'01',

        };

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleQuarterChange=(e)=>{
            let quarter =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    quarter
                }
            })
        }
        this.handleCompanyChange=(e)=>{
            let company =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    company
                }
            })
        }

        this.handleButtonClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='7', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })

        this.handleButtonNextClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='6', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })
    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee'}}>
                <Grid container style={{marginTop:30}} spacing={3}>
                    <Grid item xs={12} sm={12}>

                        <ListItem >
                            <ListItemAvatar>
                                <Avatar style={{fontSize:16}}>
                                    <FeedbackIcon  />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText>
                                <div style={{fontSize:24,color:'#0d47a1'}} className={'hwkt'}>低压能效等级份额图</div>
                            </ListItemText>
                        </ListItem>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>

                    <Grid item xs={6} sm={2}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>季度</span>
                    </Grid>

                    <Grid item xs={6} sm={2}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Quarter
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.quarter}
                                onChange={this.handleQuarterChange}
                                input={<OutlinedInput name="Year" id="outlined-quarter-simple" />}
                            >
                                {
                                    this.state.quarters.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>企业</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-company-simple">
                                Company
                            </InputLabel>
                            <Select


                                labelWidth={80}
                                value={this.state.company}
                                onChange={this.handleCompanyChange}
                                input={<OutlinedInput name="Company" id="outlined-company-simple" />}
                            >
                                {
                                    this.state.companies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={2}>
                        <Button variant="contained" color="primary">
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>

                        </Button>
                    </Grid>
                    <Grid item sm={6} style={{display:'none'}}>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonClick}>Search</Button>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonNextClick}>Search</Button>

                    </Grid>

                </Grid>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper style={{backgroundColor:'#fafafa'}}>
                    <ReactEcharts
                        option={this.state.option}
                        style={{height: '390px', width: '95%'}}/>

                </Paper>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>

                    <div style={{paddingTop:30,paddingLeft:30,fontSize:26,}} className={'hwkt'}>
                        能效等级数据
                    </div>
                    <div style={{padding:30,fontSize:26,}}>
                        <Table >
                            <TableHead>
                                <TableRow style={{backgroundColor:'black',}}>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 能效等级</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 产量(万千瓦)</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 百分比</span></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {[{key:1,quanter:'IE2',output:820,percent:'24%' },{key:2,quanter:'IE3',output:932,percent:'26%'  },{key:3,quanter:'IE4',output:901,percent:'25%'  },{key:4,quanter:'其它',output:934,percent:'25%' }].map(row => (
                                    <TableRow key={row.key}>

                                        <TableCell ><span style={{fontSize:18}} className={'hwkt'}> {row.quanter} </span> </TableCell>
                                        <TableCell >{row.output}</TableCell>
                                        <TableCell >{row.percent}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
                <Divider style={{marginBottom:30,marginTop:30}}/>
            </Container>
        );
    }
};

export default LvEfficiencyData;

