import {
    Grid,
    Table,
    TableHeaderRow,
    TableSelection,
    PagingPanel,

} from '@devexpress/dx-react-grid-material-ui';
import {Container} from '@material-ui/core'
import {
    SelectionState,
    PagingState,
    IntegratedPaging,
    IntegratedSelection,
    CustomPaging,
} from '@devexpress/dx-react-grid';

import {Divider,Paper } from '@material-ui/core';
import {
    VirtualTableState,
} from '@devexpress/dx-react-grid';
import {
    VirtualTable,
} from '@devexpress/dx-react-grid-material-ui';

import React from 'react'
import axios from "axios";
import {server} from "../globals/global";
import {notification} from "antd";

import {
    GroupingState,
    IntegratedGrouping,
} from '@devexpress/dx-react-grid';
import {
    TableGroupRow,
} from '@devexpress/dx-react-grid-material-ui';

const getRowId = row => row.F_ID;
const VIRTUAL_PAGE_SIZE = 10;
const MAX_ROWS  = 50000
export default class ReactGrid extends React.Component
{
    constructor(props)
    {
        super(props)

        this.state = {

            energyConsume:{
                columns: [
                    { name: 'F_ID', title: 'F_ID', getCellValue: row => row.F_ID},
                    { name: 'F_CREATE_TIME', title: 'F_CREATE_TIME', getCellValue: row => row.F_CREATE_TIME},
                    { name: 'F_ENERGY_ID', title: 'F_ENERGY_ID', getCellValue: row => row.F_ENERGY_ID},
                    { name: 'F_ENERGY_CONSUME', title: 'F_ENERGY_CONSUME', getCellValue: row => row.F_ENERGY_CONSUME},
                ],
                rows: [],


            },

            columns: [
                { name: 'F_ID', title: 'F_ID', getCellValue: row => row.F_ID},
                { name: 'F_CREATE_TIME', title: 'F_CREATE_TIME', getCellValue: row => row.F_CREATE_TIME},
                { name: 'F_ENERGY_ID', title: 'F_ENERGY_ID', getCellValue: row => row.F_ENERGY_ID},
                { name: 'F_DEVICE_GROUP_ID', title: 'F_DEVICE_GROUP_ID', getCellValue: row => row.F_DEVICE_GROUP_ID},

            ],
            rows: [],
            tableColumnExtensions: [
                { columnName: 'F_ID', width: 80 },
                { columnName: 'F_CREATE_TIME', width: 220 },
                { columnName: 'F_ENERGY_ID', width: 220 },
                { columnName: 'F_DEVICE_GROUP_ID', width: 120 },
            ],
            skip: 0,
            totalCount: 50000,
            loading: true,
        };

        this.getRemoteRows = (skip, take) => {

            this.DeviceGroupConsumeDayLazyLoading(skip, take)
        };

    };

    componentDidMount() {
        let params = new URLSearchParams()
        params.append('entity', JSON.stringify({skip:0,take:10}))
        //params.append('skip', 0)
        //params.append('take', 10)

        axios.post(server+ 'DeviceGroupConsume/DeviceGroupConsumeDayLazyLoading',params )
            .then(response => response)
            .then(({ data }) => {

                //return;
                this.setState((prevState,props)=>{
                    let energyConsume = Object.assign({},{columns:prevState.energyConsume.columns}, {rows:data.rows})
                    return Object.assign({},prevState, {energyConsume});
                });
            })
            .catch(() => {});
    }


    DeviceGroupConsumeDayLazyLoading(skip, take)
    {
        this.setState({ loading: true });

        let params = new URLSearchParams()
        params.append('entity', JSON.stringify({skip,take}))
        //params.append('skip', 0)
        //params.append('take', 10)

        axios.post(server+ 'DeviceGroupConsume/DeviceGroupConsumeDayLazyLoading',params )
            .then(response => response)
            .then(({ data }) => {
                this.setState({
                    skip,
                    rows: data.rows,
                    loading: false,
                    totalCount: MAX_ROWS,
                });
            })
            .catch(() => this.setState({ loading: false }));
    }

    render() {
        const {
            rows, columns, skip, totalCount, tableColumnExtensions, loading,
        } = this.state;

        return (
            <Container>
                <Grid
                    rows={rows}
                    columns={columns}
                    getRowId={getRowId}
                >
                    <VirtualTableState
                        loading={loading}
                        totalRowCount={totalCount}
                        pageSize={VIRTUAL_PAGE_SIZE}
                        skip={skip}
                        getRows={this.getRemoteRows}
                    />
                    <VirtualTable columnExtensions={tableColumnExtensions} />
                    <TableHeaderRow />

                </Grid>

                <Paper style={{marginTop:20}}>

                <Grid
                    rows={this.state.energyConsume.rows}
                    columns={this.state.energyConsume.columns}
                >
                    <GroupingState grouping={[{ columnName: 'F_ENERGY_ID' },{ columnName: 'F_CREATE_TIME' }]}/>

                    <IntegratedGrouping />
                    <Table />
                    <TableHeaderRow />
                    <TableGroupRow />
                </Grid>
                </Paper>
            </Container>
        );

    }
}

