


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import {Search as SearchIcon ,Feedback as FeedbackIcon ,EvStation as EvStationIcon ,EventSharp as EventSharpIcon ,Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";


class YearData extends React.Component {

    getOption(){
        return {
            color: ['#336699', '#6699CC', '#6699FF', '#4cabce', '#e5323e'],
            title: {
                x: '20px',
                y: '20px',
                text: "年度走势(万元)",
                subtext: 'Statistics: Annual trend',
            },

            //鼠标触发提示数量
            tooltip: {
                trigger: "axis",
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'cross'        // 默认为直线，可选为：'line' | 'shadow' | 'cross'
                },
            },
            legend: {
                data: ['年度产值'],
                y: '20px',
                x : '200px',
            },
            // 网格

            grid: {
                x: 100,
                y: 100,
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            gauge : {
                axisLine: {
                    lineStyle: {
                        color: [[0.2, '#2ec7c9'],[0.8, '#5ab1ef'],[1, '#d87a80']],
                        width: 10
                    }
                },
                axisTick: {
                    splitNumber: 10,
                    length :15,
                    lineStyle: {
                        color: 'auto'
                    }
                },
                splitLine: {
                    length :22,
                    lineStyle: {
                        color: 'auto'
                    }
                },
                pointer : {
                    width : 5
                }
            },
            //x轴显示
            xAxis: {
                type: 'category',
                data: ["2019","2020","2021","2022","2023","2024","2025"],
                splitLine: {
                    show: false
                }
            },
            //y轴显示
            yAxis: [{
                type:'value',
                splitArea : {
                    show : true,
                    areaStyle : {
                        color: ['rgba(250,250,250,0.1)','rgba(200,200,200,0.1)']
                    }
                },

                //y轴数据格式化
                axisLabel: {
                    formatter: function (value) {
                        return value;
                    }
                }
            }],
            series: [
                {
                    name:'年度产值',
                    data: [820, 932, 901, 934,1034,1134,1234],
                    type: 'line',
                    smooth: true
                }
            ]
        };
    }

    constructor(props)
    {
        super(props)
        this.state={
            option: this.getOption(),
            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},{value:2024,text:'2024'},{value:2025,text:'2025'},],
            year:2019,
            endYear:2025,
            companies:[{value:'01',text:'江苏大中'},{value:'02',text:'卧龙电驱'}],
            company:'01',

        };

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleEndYearChange=(e)=>{
            let endYear =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    endYear
                }
            })
        }
        this.handleCompanyChange=(e)=>{
            let company =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    company
                }
            })
        }

        this.handleButtonClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='7', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })

        this.handleButtonNextClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='6', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })
    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee'}}>
                <Grid container style={{marginTop:30}} spacing={3}>
                    <Grid item xs={12} sm={12}>

                        <ListItem >
                            <ListItemAvatar>
                                <Avatar style={{fontSize:16}}>
                                    <FeedbackIcon  />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText>
                                <div style={{fontSize:24,color:'#0d47a1'}} className={'hwkt'}>高效电机年度产值图</div>
                            </ListItemText>
                        </ListItem>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>

                    <Grid item xs={6} sm={2}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                起始年份
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={6} sm={2}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                截止年份
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.endYear}
                                onChange={this.handleEndYearChange}
                                input={<OutlinedInput name="Year" id="outlined-endyear-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>企业</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-company-simple">
                                Company
                            </InputLabel>
                            <Select


                                labelWidth={80}
                                value={this.state.company}
                                onChange={this.handleCompanyChange}
                                input={<OutlinedInput name="Company" id="outlined-company-simple" />}
                            >
                                {
                                    this.state.companies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={3}>
                        <Button variant="contained" color="primary">
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>

                        </Button>
                    </Grid>
                    <Grid item sm={6} style={{display:'none'}}>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonClick}>Search</Button>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonNextClick}>Search</Button>

                    </Grid>

                </Grid>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>
                    <ReactEcharts
                        option={this.state.option}
                        style={{height: '350px', width: '95%'}}/>

                </Paper>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>

                    <div style={{paddingTop:30,paddingLeft:30,fontSize:26,}} className={'hwkt'}>
                        年度数据
                    </div>
                    <div style={{padding:30,fontSize:26,}}>
                        <Table >
                            <TableHead>
                                <TableRow style={{backgroundColor:'black',}}>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 年度</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 产值(万元)</span></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {[{key:1,year:'2019',output:820, },{key:2,year:'2020',output:932, },{key:3,year:'2021',output:901, },{key:4,year:'2022',output:934}
                                    ,{key:5,year:'2023',output:1034},{key:6,year:'2024',output:1134},{key:7,year:'2025',output:1234}
                                ].map(row => (
                                    <TableRow key={row.key}>

                                        <TableCell ><span style={{fontSize:18}} className={'hwkt'}> {row.year} </span> </TableCell>
                                        <TableCell >{row.output}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
                <Divider style={{marginBottom:30,marginTop:30}}/>
            </Container>
        );
    }
};

export default YearData;

