import {connect} from "react-redux";
import Main from "./main";
import Login from "./login";

import {action_logout} from '../actions/userAction'
import {action_setDefaultKeys,action_resetDefaultKeys} from "../actions/menuAction";

const mapStateToProps= (state)=>{
    return ({
        validated : state.user.validated,
        defaultSelectedKeys : state.menu.defaultSelectedKeys,
        defaultOpenKeys: state.menu.defaultOpenKeys,
    });

}
const mapDispatchToProps=(dispatch)=>{
    return ({
        logout:()=>dispatch(action_logout()),
        setDefaultKeys:(defaultKeys)=>dispatch(action_setDefaultKeys(defaultKeys)),
        resetDefaultKeys:()=>{dispatch(action_resetDefaultKeys())}
    });
}

export default connect(mapStateToProps,mapDispatchToProps)(Main)
