
import React from 'react'
import {Layout, Menu,Sider} from "antd/lib";
import {Icon} from "antd";
import {Link} from "react-router-dom";

const { SubMenu } = Menu;

export default class SiderMenu extends React.Component
{

    rootSubmenuKeys = ['sub1', 'sub2', 'sub3'];

    constructor(props)
    {
        super(props);
        this.state = {
            openKeys: ['sub1'],

        };
        this.handleClick=(e)=>{

            let {keyPath} = e
            this.props.setDefaultKeys({defaultSelectedKeys:[keyPath[0]],defaultOpenKeys:[keyPath[1]]});
        }


    }

    onOpenChange = openKeys => {
        const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
        if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
            this.setState({ openKeys });
        } else {
            this.setState({
                openKeys: latestOpenKey ? [latestOpenKey] : [],
            });
        }
    };


    render() {
        return(

            <Menu
                theme={'dark'}
                mode="inline"
                defaultSelectedKeys={this.props.defaultSelectedKeys}
                defaultOpenKeys={this.props.defaultOpenKeys}
                openKeys={this.state.openKeys}
                style={{ height: '100%' }}
                onOpenChange={this.onOpenChange}
                onClick={this.handleClick}

            >

                <SubMenu
                    key="sub1"
                    title={
                        <div >
                            <Icon type="user" />
                            <span className={'hwkt md-f'}>基本信息</span>
                        </div>
                    }
                >
                    <Menu.Item key="1">
                        <Link to={"/"}><span className={'hwkt md-f'}>综合首页</span></Link>
                    </Menu.Item>
                    <Menu.Item key="2">
                        <Link to={"/MenuDemo"}>MenuDemo</Link>
                    </Menu.Item>
                    <Menu.Item key="3">option3</Menu.Item>
                    <Menu.Item key="4">option4</Menu.Item>
                </SubMenu>
                <SubMenu
                    key="sub2"
                    title={
                        <div>
                            <Icon type="laptop" />
                            <span>subnav 2</span>
                        </div>

                    }
                >
                    <Menu.Item key="5">option5</Menu.Item>
                    <Menu.Item key="6">option6</Menu.Item>
                    <Menu.Item key="7">option7</Menu.Item>
                    <Menu.Item key="8">option8</Menu.Item>
                </SubMenu>
                <SubMenu
                    key="sub3"
                    title={
                        <div>
                            <Icon type="notification" />
                            <span>subnav 3</span>
                        </div>
                    }>
                    <Menu.Item key="9">option9</Menu.Item>
                    <Menu.Item key="10">option10</Menu.Item>
                    <Menu.Item key="11">option11</Menu.Item>
                    <Menu.Item key="12">option12</Menu.Item>
                </SubMenu>
            </Menu>
        );
    }

}






