


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import {Search as SearchIcon ,Feedback as FeedbackIcon ,EvStation as EvStationIcon ,EventSharp as EventSharpIcon ,Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon,
    Explore as ExploreIcon,
    SaveAlt as SaveAltIcon,
} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";


class PriceSearch extends React.Component {


    constructor(props)
    {
        super(props)
        this.state={

            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            companies:[{value:'01',text:'江苏大中'},{value:'02',text:'卧龙电驱'}],
            company:'01',

        };

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleCompanyChange=(e)=>{
            let company =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    company
                }
            })
        }


    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee'}}>
                <Grid container style={{marginTop:30}} spacing={3}>
                    <Grid item xs={12} sm={12}>

                        <ListItem >
                            <ListItemAvatar>
                                <Avatar style={{fontSize:16}}>
                                    <FeedbackIcon  />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText>
                                <div style={{fontSize:24,color:'#0d47a1'}} className={'hwkt'}>电机材料价格查询</div>
                            </ListItemText>
                        </ListItem>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>企业</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-company-simple">
                                Company
                            </InputLabel>
                            <Select


                                labelWidth={80}
                                value={this.state.company}
                                onChange={this.handleCompanyChange}
                                input={<OutlinedInput name="Company" id="outlined-company-simple" />}
                            >
                                {
                                    this.state.companies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={2}>
                        <Button variant="contained" color="primary">
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>

                        </Button>
                    </Grid>

                    <Grid item xs={6} sm={2}>
                        <Button variant="contained" color="secondary">
                            <SaveAltIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>下载</span>

                        </Button>
                    </Grid>
                </Grid>


                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>

                    <div style={{padding:30,fontSize:20,}}>
                        <Table >
                            <TableHead>
                                <TableRow style={{backgroundColor:'black',}}>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 月份</span></TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>电磁线</div>
                                            <div>(吨/元)</div>
                                        </div>
                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>硅钢片</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>碳结钢</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>铸铁件</div>
                                            <div>(吨/元)</div>
                                        </div>
                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>铝锭</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {[{key:1,month:'1月',magnetWire:820,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:2,month:'2月',magnetWire:932,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901 ,aluminiumIngot:934},
                                    {key:3,month:'3月',magnetWire:901,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:4,month:'4月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:5,month:'5月',magnetWire:820, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:6,month:'6月',magnetWire:932, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:7,month:'7月',magnetWire:901, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:8,month:'8月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:9,month:'9月',magnetWire:820,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:10,month:'10月',magnetWire:932,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:11,month:'11月',magnetWire:901, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:12,month:'12月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                ].map(row => (
                                    <TableRow key={row.key}>

                                        <TableCell ><span style={{fontSize:18}} className={'hwkt'}> {row.month} </span> </TableCell>
                                        <TableCell >{row.magnetWire}</TableCell>
                                        <TableCell >{row.siliconSheets}</TableCell>
                                        <TableCell >{row.carbonStructuralSteel}</TableCell>
                                        <TableCell >{row.ironCasting}</TableCell>
                                        <TableCell >{row.aluminiumIngot}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
                <Divider style={{marginBottom:30,marginTop:30}}/>
            </Container>
        );
    }
};

export default PriceSearch;

