import {connect} from "react-redux";
import App from "./App";


import {action_validateSuccess,action_validateFail,action_setAccount,action_setAccountNo,action_setPassword} from './actions/userAction'

const mapStateToProps= (state)=>{
    return ({
        F_ACCOUNT_NO : state.user.F_ACCOUNT_NO,
        F_PASSWORD:state.user.F_PASSWORD,
        validated:state.user.validated,
    });

}



const mapDispatchToProps=(dispatch)=>{
    return ({
        validateSuccess:()=>dispatch(action_validateSuccess()),
        validateFail:()=>dispatch(action_validateFail()),
        setAccount:(F_ACCOUNT_NO,F_PASSWORD)=>dispatch(action_setAccount(F_ACCOUNT_NO,F_PASSWORD)),
        setAccountNo:(F_ACCOUNT_NO)=>dispatch(action_setAccountNo(F_ACCOUNT_NO)),
        setPassword:(F_PASSWORD)=>dispatch(action_setPassword(F_PASSWORD)),

    });
}

export default connect(mapStateToProps,mapDispatchToProps)(App)
