
import React from 'react'
import {Button} from 'antd'

export default class FileDonwload extends React.Component
{

    constructor(props)
    {
        super(props)
        this.handleClick =()=>{
            alert('hello')
            window.location = 'http://localhost:3010/Excel/DeviceGroupEnergyReportDownload' + '?entity=' + JSON.stringify({
                F_CORPORATION_CODE :'C1',
                F_FACTORY_CODE:'F00',
                F_WORKSHOP_CODE:'W00',
                F_SECTION_CODE : 'S00',
                F_TEAM_CODE:'T00',
                F_ENERGY_CONSUME_TYPE:1,
                F_YEAR:'2019',
                F_MONTH:'',
                F_WEEK:'',
                F_DAY:''
            });
        }
    }

    render() {

        return (<div>
            <Button type="primary" icon="download"  shape="round" onClick={this.handleClick}>
                Download
            </Button>
        </div>)
    }
}

