import axios from "axios";
import {server} from "../globals/global";
import {notification} from "antd";


const energyTypeEntity={
    getEnergyType :()=>{
        let params = new URLSearchParams()
        //params.append('entity', JSON.stringify({F_ACCOUNT_NO,F_PASSWORD}))
        return axios.post(server+ 'EnergyType/GetEnergyType',params )
    },
    getEnergyTypeEnable:()=>{
        let params = new URLSearchParams()
        return axios.post(server+ 'EnergyType/GetEnergyTypeEnable',params )
    },
    enterpriseHomePage:()=>{
        let params = new URLSearchParams()
        params.append('entity', JSON.stringify({}))
        return axios.post(server+ 'Enterprise/EnterpriseHomePage',params )
    }
}


export default energyTypeEntity;