

const userReducer = (state={validated:false,F_ACCOUNT_NO:'',F_PASSWORD:'',F_COMPANY_ID:''},action)=>{

    switch (action.type) {
        case "validateSuccess":
            return {
                ...state,
                validated: true,
            }
            break;
        case "validateFail":
            return {
                ...state,
                validated: false,
            }
            break;
        case "setAccountNo":
            return {
                ...state,
                F_ACCOUNT_NO: action.payload,
            }
            break;
        case "setPassword":
            return {
                ...state,
                F_PASSWORD: action.payload,
            }
            break;
        case "setCompanyID":
            return {
                ...state,
                F_COMPANY_ID: action.payload,
            }
            break;
        case "setAccount":
            return {
                ...state,
                ...action.payload,
            }
            break;
        case "logout":
            return {
                ...state,
                validated: false,
            }
            break;

        default:
            return state;
    }

}

export {userReducer}
