import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';


import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';
import { deepOrange, deepPurple } from '@material-ui/core/colors';

import {Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon,Favorite as FavoriteIcon,PersonPin as PersonPinIcon} from '@material-ui/icons';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';


import {Button} from "@material-ui/core";


export default class PriceInput extends React.Component
{
    getLoads=()=>
    {
        return [
            {value:1,text:'风机'},
            {value:2,text:'水泵'},
            {value:3,text:'压缩机'},
            {value:4,text:'其它设备'},

        ];

    };

    getEfficiencies=(machineryType)=>
    {
        if (machineryType==3)
        {
            return [
                {value:4,text:'三级能效'},
                {value:5,text:'二级能效'},
                {value:6,text:'一级能效'},
            ];

        }
        if (machineryType==4)
        {
            return [
                {value:0,text:''},

            ];

        }
        return [
            {value:1,text:'IE2'},
            {value:2,text:'IE3'},
            {value:3,text:'IE4'},

        ];

    };
    getVoltages=(machineryType)=>
    {

        if (machineryType==3)
        {
            return [
                {value:4,text:'6KV'},
                {value:5,text:'10KV'},
            ];

        }
        if (machineryType==4)
        {
            return [
                {value:0,text:''},

            ];

        }
        return [
            {value:1,text:'380'},
            {value:2,text:'660'},
            {value:3,text:'其它'},

        ];

    }

    constructor(props)
    {
        super(props);


        let machineryType =1;
        let load = 1;
        let voltages = this.getVoltages(machineryType);
        let voltage= voltages[0].value

        let efficiencies =this.getEfficiencies(machineryType);
        let efficiency= efficiencies[0].value

        let year = 2019

        this.state = {
            machineryTypes:[{value:1,text:'低压高效'},{value:2,text:'高效永磁'},{value:3,text:'高压高效'},{value:4,text:'其它'},],
            machineryType,
            voltages,
            voltage,
            efficiencies,
            efficiency,
            loads:[{value:1,text:'风机'},{value:2,text:'水泵'},{value:3,text:'高压高效'},{value:4,text:'其它'},],
            load,
            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            months:[{value:1,text:'1'},{value:2,text:'2'},{value:3,text:'3'},{value:4,text:'4'},{value:5,text:'5'},{value:6,text:'6'},{value:7,text:'7'},{value:8,text:'8'},{value:9,text:'9'},{value:10,text:'10'},{value:11,text:'11'},{value:12,text:'12'},],
            month:1

        }

        this.handleMachineryTypeChange=(e)=>{
            let machineryType =  e.target.value
            let voltages = this.getVoltages(machineryType);
            let voltage= voltages[0].value
            let efficiencies =this.getEfficiencies(machineryType);
            let efficiency= efficiencies[0].value

            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    machineryType,
                    voltages,
                    voltage,
                    efficiencies,
                    efficiency
                }
            })
        };
        this.handleVoltageChange=(e)=>{
            let voltage =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    voltage
                }
            })
        };
        this.handleEfficiencyChange=(e)=>{
            let efficiency =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    efficiency
                }
            })
        }

        this.handleLoadChange=(e)=>{
            let load =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    load
                }
            })
        }

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleMonthChange=(e)=>{
            let month =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    month
                }
            })
        }
    }

    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee',marginTop:30}}>


                <div >

                    <ListItem >
                        <ListItemAvatar>
                            <Avatar style={{backgroundColor:deepOrange[500]}}>
                                <PersonPinIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>
                            <div style={{fontSize:26,marginTop:20,marginBottom:20,color:deepOrange[500]}}  className={'hwkt'}>
                                <div>高效电机材料价格</div>

                            </div>
                        </ListItemText>
                    </ListItem>
                </div>

                <Divider style={{marginBottom:20}}/>

                <Grid container spacing={3}>
                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>
                    <Grid item xs={8} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>月份</span>
                    </Grid>
                    <Grid item xs={8} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-month-simple">
                                Month
                            </InputLabel>
                            <Select
                                MenuProps={{PaperProps: {
                                        style: {
                                            maxHeight: 48* 4.5,

                                        },
                                    }}}
                                labelWidth={80}
                                value={this.state.month}
                                onChange={this.handleMonthChange}
                                input={<OutlinedInput name="Month" id="outlined-month-simple" />}
                            >
                                {
                                    this.state.months.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>





                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>电磁线(吨/元)</span>
                    </Grid>

                    <Grid item xs={8} sm={3}>
                        <TextField
                            required
                            id="magnetWire"
                            name="magnetWire"
                            label="magnetWire"
                            fullWidth
                            autoComplete="magnetWire"
                            variant="outlined"
                            type={'number'}
                        />
                    </Grid>

                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>硅钢片(吨/元)</span>
                    </Grid>

                    <Grid item xs={8} sm={3}>
                        <TextField
                            required
                            id="siliconSheets"
                            name="siliconSheets"
                            label="siliconSheets"
                            fullWidth
                            autoComplete="siliconSheets"
                            variant="outlined"
                            type={'number'}
                        />
                    </Grid>

                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>碳结钢(吨/元)</span>
                    </Grid>

                    <Grid item xs={8} sm={3}>
                        <TextField
                            required
                            id="carbonStructuralSteel"
                            name="carbonStructuralSteel"
                            label="carbonStructuralSteel"
                            fullWidth
                            autoComplete="carbonStructuralSteel"
                            variant="outlined"
                            type={'number'}
                        />
                    </Grid>

                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>铸铁件(吨/元)</span>
                    </Grid>

                    <Grid item xs={8} sm={3}>
                        <TextField
                            required
                            id="ironCasting"
                            name="ironCasting"
                            label="ironCasting"
                            fullWidth
                            autoComplete="ironCasting"
                            variant="outlined"
                            type={'number'}
                        />
                    </Grid>

                    <Grid item xs={4} sm={3}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>铝锭(吨/元)</span>
                    </Grid>

                    <Grid item xs={8} sm={3}>
                        <TextField
                            required
                            id="aluminiumIngot"
                            name="aluminiumIngot"
                            label="aluminiumIngot"
                            fullWidth
                            autoComplete="aluminiumIngot"
                            variant="outlined"
                            type={'number'}
                        />
                    </Grid>

                    <Grid item xs={10} sm={10}>

                    </Grid>
                    <Grid item xs={2} sm={2}>
                        <Button variant="contained" color="primary" >
                            <SaveIcon />
                            <span style={{padding:10,fontSize:20}} className={'hwzs'}>保存</span>

                        </Button>
                    </Grid>

                </Grid>
            </Container>

        )
    }
}

