import React, {Component} from 'react';

import {
    Divider,
    IconButton,
    TextField,
    FormControl,
    Input,
    FormHelperText,
    InputAdornment,
    Button,
    Container,
    ListItemAvatar,
    ListItemText,
    Avatar,
    List,
    Grid,
    ListItem,
    ListItemIcon,

} from '@material-ui/core'

import {
    Person as PersonIcon,
    AcUnit as AcUnitIcon,
    Save as SaveIcon,
    PersonPin as PersonPinIcon,
    CloudUpload as CloudUploadIcon,
    SelectAllOutlined as SelectAllOutlinedIcon,
    Inbox as InboxIcon,
    Drafts as DraftsIcon,
    AttachFile as AttachFileIcon,
    ArrowDropDown as ArrowDropDownIcon,
    ArrowDropDownCircleOutlined as ArrowDropDownCircleOutlinedIcon
} from '@material-ui/icons';
import axios from 'axios'

import {deepOrange} from "@material-ui/core/colors";

import {server} from '../globals/global'


class FetchUpload extends Component {
    constructor(props) {
        super(props);
        this.fileInput = React.createRef();

        this.state={
            filename:'',
        }
    }

    change=()=>{
        console.info(this.fileInput)
        if (this.fileInput.current.files[0])
        {

            let filename=this.fileInput.current.files[0].name;
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    filename
                }
            })
        }
        else
        {
            console.info('file is null')
            let filename=''
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    filename
                }
            })
        }

    }

    download=()=>{
        window.location = server + 'Excel/DeviceGroupEnergyReportDownload' + '?entity=' + JSON.stringify({
            F_CORPORATION_CODE :'C1',
            F_FACTORY_CODE:'F00',
            F_WORKSHOP_CODE:'W00',
            F_SECTION_CODE : 'S00',
            F_TEAM_CODE:'T00',
            F_ENERGY_CONSUME_TYPE:1,
            F_YEAR:'2019',
            F_MONTH:'',
            F_WEEK:'',
            F_DAY:''
        });
    }

    upload = () => {
        const data = new FormData();
        data.append('file', this.fileInput.current.files[0]);  //相当于 input:file 中的name属性
        data.append('F_ID', "1");  //相当于 input:file 中的name属性
        data.append('F_SYSTEM_NAME', "F_SYSTEM_NAME");  //相当于 input:file 中的name属性


        /*
        fetch('http://localhost:3010/Company/Upload', {
            method: 'POST',
            body: data
        }).then(response => console.log(response))
        */

        axios.post(server + 'Company/Upload', data)
            .then(response => console.log(response))

    };


    render() {
        return (
            <Container  maxWidth={'md'} style={{backgroundColor:'#eee',marginTop:30,padding:30}}>

                <List>

                    <ListItem >
                        <ListItemAvatar>
                            <Avatar style={{backgroundColor:deepOrange[500]}}>
                                <PersonPinIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>
                            <div style={{fontSize:26,marginTop:20,marginBottom:20,color:deepOrange[500]}}  className={'hwkt'}>
                                <div>月度数据上传</div>

                            </div>
                        </ListItemText>
                    </ListItem>
                </List>


                <TextField
                    id="outlined-adornment-password"

                    fullWidth
                    variant="outlined"

                    value={this.state.filename}
                    label="Choose a file"
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <IconButton
                                    edge="end"
                                    aria-label="Toggle password visibility"

                                >

                                    <label className="MuiButtonBase-root MuiButton-root MuiButton-contained MuiButton-containedPrimary" htmlFor="monthUploadFile" style={{padding:13}} >
                                        <AttachFileIcon />
                                    </label>
                                </IconButton>
                            </InputAdornment>
                        ),
                    }}
                />

                <input  type="file" name='file' ref={this.fileInput} style={{height:20,display:'none'}} id='monthUploadFile' accept="application/vnd.ms-excel"  onChange={this.change}/>

                <Divider style={{marginTop:30,marginBottom:30}}/>

                <Grid container>
                    <Grid item sm={3}>
                        <Button variant={'contained'} color={'primary'} onClick={this.upload}>
                            <CloudUploadIcon />
                            <span style={{padding:10,fontSize:20}} className={'hwzs'}>上传</span>
                        </Button>

                    </Grid>
                    <Grid item sm={3}>
                        <Button variant={'contained'} color={'secondary'} onClick={this.download}>
                            <ArrowDropDownCircleOutlinedIcon/>
                            <span style={{padding:10,fontSize:20}} className={'hwzs'}>下载格式文件</span>
                        </Button>

                    </Grid>

                </Grid>



            </Container>
        )
    }
}
export default FetchUpload;