


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';


import {Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";
import {notification} from "antd";

class WorkunitRecudedConsumePieByPurpose extends React.Component {

    getOption(){
        return {
            //color: ['#003366', '#006699', '#4cabce', '#e5323e'],
            //color: ['red', 'green', 'orange', '#C0C0C0'],
            color: ['#336699', '#6699CC', '#6699FF', '#4cabce', '#e5323e'],
            title: {
                x: '20px',
                y: '20px',
                text: '综合能耗--用途分析',
                subtext : "Analysis: By purpose"
            },
            tooltip: {
                show: true,
                trigger: 'item',


            },
            //阴影的配置
            itemStyle: {
                normal: {
                    //扇形的颜色
                    //color: '#c23531',
                    shadowBlur: 50,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                emphasis: {
                    shadowBlur: 200,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            },
            legend: {
                bottom: 0,
                left: 'center',
                data: [],
                selected: {},
            },
            xAxis: [

            ],
            yAxis: [

            ],
            series: [
                {
                    "name": "能耗",
                    "type": "pie",//bar line
                    "data": [],
                    "radius": ['0', '60%'],
                    label: {            //饼图图形上的文本标签
                        normal: {

                            backgroundColor: "transparent",            //标题背景色
                            //提示框浮层内容格式器，支持字符串模板和回调函数两种形式,
                            //模板变量有 {a}, {b}，{c}，{d}，{e}，分别表示系列名，数据名，数据值等
                            formatter: '{b}\n{d}%',

                        }
                    },
                }

            ]
        }
    }

    constructor(props)
    {
        super(props)
        this.state={
            option: this.getOption()
        };


        this.echarts =<ReactEcharts
            option={this.state.option}
            style={{height: '350px', width: '1000px'}} />

        this.handleButtonClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='7', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitRecudedConsumePieByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let option = this.getOption();
                    option.series[0].data = data.rows;
                    option.color = data.color;
                    option.legend.data = data.legend.data;

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })

        this.handleButtonNextClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='6', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitRecudedConsumePieByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let option = this.getOption();
                    option.series[0].data = data.rows;
                    option.color = data.color;
                    option.legend.data = data.legend.data;

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })
    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} >
                <Grid container style={{marginTop:30}}>
                    <Grid item sm={6}>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonClick}>Search</Button>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonNextClick}>Search</Button>

                    </Grid>

                </Grid>

                <Paper>
                    <ReactEcharts
                        option={this.state.option}
                        style={{height: '350px', width: '95%'}}/>

                </Paper>

            </Container>
        );
    }
};

export default WorkunitRecudedConsumePieByPurpose;

