


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import {Search as SearchIcon ,Feedback as FeedbackIcon ,EvStation as EvStationIcon ,EventSharp as EventSharpIcon ,Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";


class PriceData extends React.Component {

    getOption(){
        return {
            color: ['#336699', '#6699CC', '#6699FF', '#4cabce', '#e5323e'],
            title: {
                x: '20px',
                y: '20px',
                text: "材料价格走势(吨/元)",
                subtext: 'Statistics: Material Price Tren',
            },

            //鼠标触发提示数量
            tooltip: {
                trigger: "axis",
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'cross'        // 默认为直线，可选为：'line' | 'shadow' | 'cross'
                },
            },
            legend: {
                data: ['电磁线','硅钢片','碳结钢','铸铁件','铝锭'],
                y: '20px',
                x : '200px',
            },
            // 网格

            grid: {
                x: 100,
                y: 100,
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            gauge : {
                axisLine: {
                    lineStyle: {
                        color: [[0.2, '#2ec7c9'],[0.8, '#5ab1ef'],[1, '#d87a80']],
                        width: 10
                    }
                },
                axisTick: {
                    splitNumber: 10,
                    length :15,
                    lineStyle: {
                        color: 'auto'
                    }
                },
                splitLine: {
                    length :22,
                    lineStyle: {
                        color: 'auto'
                    }
                },
                pointer : {
                    width : 5
                }
            },
            //x轴显示
            xAxis: {
                type: 'category',
                data: ["1月","2月","3月","4月","5月","6月","7月","8月","9月","10月","11月","12月"],
                splitLine: {
                    show: false
                }
            },
            //y轴显示
            yAxis: [{
                type:'value',
                splitArea : {
                    show : true,
                    areaStyle : {
                        color: ['rgba(250,250,250,0.1)','rgba(200,200,200,0.1)']
                    }
                },

                //y轴数据格式化
                axisLabel: {
                    formatter: function (value) {
                        return value;
                    }
                }
            }],
            series: [
                {
                    name:'电磁线',
                    data: [41.1, 30.4, 65.1, 53.3, 83.8, 98.7,41.1, 30.4, 65.1, 53.3, 83.8, 98.7],
                    type: 'line',
                    smooth: true
                },
                {
                    name:'硅钢片',
                    data: [86.5, 92.1, 85.7, 83.1, 73.4, 55.1,86.5, 92.1, 85.7, 83.1, 73.4, 55.1],
                    type: 'line',
                    smooth: true
                },
                {
                    name:'碳结钢',
                    data: [24.1, 67.2, 79.5, 86.4, 65.2, 82.5,24.1, 67.2, 79.5, 86.4, 65.2, 82.5],
                    type: 'line',
                    smooth: true
                },
                {
                    name:'铸铁件',
                    data: [55.2, 67.1, 69.2, 72.4, 53.9, 39.1,55.2, 67.1, 69.2, 72.4, 53.9, 39.1],
                    type: 'line',
                    smooth: true
                },
                {
                    name:'铝锭',
                    data: [55.2, 67.1, 69.2, 72.4, 153.9, 139.1,124.1, 157.2, 179.5, 186.4, 165.2, 182.5],
                    type: 'line',
                    smooth: true
                }
            ]
        };
    }

    constructor(props)
    {
        super(props)
        this.state={
            option: this.getOption(),
            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            companies:[{value:'01',text:'江苏大中'},{value:'02',text:'卧龙电驱'}],
            company:'01',

        };

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleCompanyChange=(e)=>{
            let company =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    company
                }
            })
        }

        this.handleButtonClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='7', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })

        this.handleButtonNextClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='6', F_WEEK='', F_DAY='8'
            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/WorkunitReducedConsumeStackByPurpose',params )
                .then(response => response)
                .then(({ data }) => {

                    let serieLabel = {
                        normal: {
                            show: false,//不显示数值
                            position: 'inside',
                            formatter: function (data) {

                                if (data.value == 0)
                                    return '';
                                else

                                    return data.value;
                            },
                        }
                    };

                    let option = this.getOption();

                    option.color = data.color;
                    //指定图例
                    option.legend.data = data.legend.data;
                    //指定x轴
                    option.xAxis.data = data.xAxis.data;
                    option.series = data.series;
                    option.series.forEach(function (item, index, arr) {
                        item.label = serieLabel;
                        //item.itemStyle.normal.label.formatter = function (a, b, c) { return c + '%'; };
                    });

                    option.tooltip.formatter = function (params, ticket, callback) {
                        var result = params[0].name + "<br>";
                        params.forEach(function (val, index) {

                            result = result + params[index].marker + val.seriesName + ":" + val.value + '<br/>';
                        });

                        return result;
                    }

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })


                })
                .catch(function (error) {
                    console.log(error);
                });
        })
    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee'}}>
                <Grid container style={{marginTop:30}} spacing={3}>
                    <Grid item xs={12} sm={12}>

                        <ListItem >
                            <ListItemAvatar>
                                <Avatar style={{fontSize:16}}>
                                    <FeedbackIcon  />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText>
                                <div style={{fontSize:24,color:'#0d47a1'}} className={'hwkt'}>材料价格走势图</div>
                            </ListItemText>
                        </ListItem>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>企业</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-company-simple">
                                Company
                            </InputLabel>
                            <Select


                                labelWidth={80}
                                value={this.state.company}
                                onChange={this.handleCompanyChange}
                                input={<OutlinedInput name="Company" id="outlined-company-simple" />}
                            >
                                {
                                    this.state.companies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={3}>
                        <Button variant="contained" color="primary">
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>

                        </Button>
                    </Grid>
                    <Grid item sm={6} style={{display:'none'}}>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonClick}>Search</Button>
                        <Button variant={'contained'} color={'primary'} onClick={this.handleButtonNextClick}>Search</Button>

                    </Grid>

                </Grid>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>
                    <ReactEcharts
                        option={this.state.option}
                        style={{height: '350px', width: '95%'}}/>

                </Paper>

                <Divider style={{marginBottom:30,marginTop:30}}/>

                <Paper>

                    <div style={{paddingTop:30,paddingLeft:30,fontSize:26,}} className={'hwkt'}>
                        月度数据
                    </div>
                    <div style={{padding:30,fontSize:20,}}>
                        <Table >
                            <TableHead>
                                <TableRow style={{backgroundColor:'black',}}>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 月份</span></TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>电磁线</div>
                                            <div>(吨/元)</div>
                                        </div>
                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>硅钢片</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>碳结钢</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>铸铁件</div>
                                            <div>(吨/元)</div>
                                        </div>
                                    </TableCell>
                                    <TableCell >
                                        <div style={{display:'flex',flexDirection:'column',color:'white',fontSize:20}} className={'hwkt'}>
                                            <div>铝锭</div>
                                            <div>(吨/元)</div>
                                        </div>

                                    </TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {[{key:1,month:'1月',magnetWire:820,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:2,month:'2月',magnetWire:932,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901 ,aluminiumIngot:934},
                                    {key:3,month:'3月',magnetWire:901,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:4,month:'4月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:5,month:'5月',magnetWire:820, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:6,month:'6月',magnetWire:932, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:7,month:'7月',magnetWire:901, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:8,month:'8月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:9,month:'9月',magnetWire:820,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:10,month:'10月',magnetWire:932,siliconSheets:820 ,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:11,month:'11月',magnetWire:901, siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                    {key:12,month:'12月',magnetWire:934,siliconSheets:820,carbonStructuralSteel:932,ironCasting:901,aluminiumIngot:934},
                                ].map(row => (
                                    <TableRow key={row.key}>

                                        <TableCell ><span style={{fontSize:18}} className={'hwkt'}> {row.month} </span> </TableCell>
                                        <TableCell >{row.magnetWire}</TableCell>
                                        <TableCell >{row.siliconSheets}</TableCell>
                                        <TableCell >{row.carbonStructuralSteel}</TableCell>
                                        <TableCell >{row.ironCasting}</TableCell>
                                        <TableCell >{row.aluminiumIngot}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
                <Divider style={{marginBottom:30,marginTop:30}}/>
            </Container>
        );
    }
};

export default PriceData;

