import React from 'react'

import {Container,Grid,TextField,Button,InputLabel,Select,OutlinedInput,MenuItem,FormControl} from '@material-ui/core'
import {Search as SearchIcon} from "@material-ui/icons";

export default  class FormFacade extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            number:10,
            text:'',
            quarter:1,
            quarters:[{value:1,text:'一季度'},{value:2,text:'二季度'},{value:3,text:'三季度'}],

        };

        this.handleAdd=()=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    number: prevState.number+10
                };
            });
        };

        this.handleNumberChange=(event)=>{
            let reg = /^\d+$/;
            let value = event.target.value;
            let number = 0;
            if (reg.exec(value))
            {
                number = parseInt(value);
            }
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    number
                };
            });

        };

        this.handleTextChange=(event)=>{

            let text = event.target.value;
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    text
                };
            });

        };

        this.handleQuarterChange=(event)=>{

            let quarter = event.target.value;
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    quarter
                };
            });
        };

        this.handleButtonClick=(event)=>{
            console.log('handleButtonClick');
        }
    }

    render() {
        return (
            <Container maxWidth={'xl'} style={{backgroundColor:'#fafafa'}}>
                <Grid container spacing={3}>
                    <Grid item sm={3}>
                        <span style={{fontSize:22,lineHeight:2,fontFamily:'华文中宋'}} >整数</span>
                    </Grid>
                    <Grid item sm={3}>
                        <div style={{display:'flex'}}>
                            <Button variant="outlined" color="primary">
                                <span style={{fontSize:'24px',fontFamily:'华文中宋'}}>减</span>
                            </Button>
                            <TextField
                                id="standard-number"
                                variant="outlined"
                                value={this.state.number}
                                onChange={this.handleNumberChange}
                            />
                            <Button variant="outlined" color="primary" onClick={this.handleAdd}>
                                <span style={{fontSize:'24px',fontFamily:'华文中宋'}}>加</span>
                            </Button>
                        </div>
                    </Grid>
                    <Grid item sm={3}>
                        <span style={{fontSize:22,lineHeight:2,fontFamily:'华文中宋'}} >文本</span>
                    </Grid>
                    <Grid item sm={3}>
                        <div style={{display:'flex'}}>
                            <TextField
                                id="standard-text"
                                variant="outlined"
                                value={this.state.text}
                                onChange={this.handleTextChange}
                            />
                        </div>
                    </Grid>
                    <Grid item sm={3}>
                        <span style={{fontSize:22,lineHeight:2,fontFamily:'华文中宋'}} >季度</span>
                    </Grid>
                    <Grid item sm={3}>
                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                            </InputLabel>
                            <Select
                                labelWidth={0}
                                value={this.state.quarter}
                                onChange={this.handleQuarterChange}
                                input={<OutlinedInput name="Year" id="outlined-quarter-simple" />}
                            >
                                {
                                    this.state.quarters.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item sm={3}>
                    </Grid>
                    <Grid item sm={3}>
                        <Button variant="contained" color="primary" onClick={this.handleButtonClick}>
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>
                        </Button>
                    </Grid>
                </Grid>
            </Container>
        );
    }
}

