import cnLocale from "date-fns/locale/zh-CN";

import 'date-fns';

import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';

const useStyles = makeStyles({
    grid: {
        width: '60%',
    },
});


/*

https://material-ui-pickers.dev/localization/date-fns

// via npm
npm i material-ui-pickers

// via yarn
yarn add material-ui-pickers

npm i date-fns@next @date-io/date-fns
// or
npm i moment @date-io/moment
// or
npm i luxon @date-io/luxon
// or
npm i dayjs @date-io/dayjs
* */

export default function MaterialUIPickers() {
    // The first commit of Material-UI
    const [selectedDate, setSelectedDate] = React.useState(new Date('2019-08-18T01:00:00'));

    const classes = useStyles();

    function handleDateChange(date) {
        setSelectedDate(date);
        console.info(date)
    }

    return (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={cnLocale}>
            <Grid container className={classes.grid} justify="space-around">
                <KeyboardDatePicker
                    margin="normal"
                    id="mui-pickers-date"
                    label="日期picker"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                        'aria-label': 'change date',
                    }}
                    format={ "yyyy-MM-dd"}
                />
                <KeyboardTimePicker

                    margin="normal"
                    id="mui-pickers-time"
                    label="时间picker"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                        'aria-label': 'change time',
                    }}

                />
            </Grid>
        </MuiPickersUtilsProvider>
    );
}