import React from  'react'

import WaterPump from '../images/WaterPump.png'
import zeekong from '../images/zeekong.jpg'

import '../css/materialUIDemo.css'
import axios from "axios";
import {server} from "../globals/global";
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'

export default class Overview extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            locations:[],
            modelOpen:false,
            counter:0

        };

        this.handleModalClose=()=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    modelOpen:false,
                }
            });
        };

        this.handleModalOpen=()=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    modelOpen:true,

                }
            });
        };

    }


    componentDidMount() {

        this.interval = setInterval(()=>{
            let params = new URLSearchParams()
            params.append('entity', JSON.stringify({F_X:0,F_Y:0}))
            axios.post(server+ 'Common/SelectLocation',params )
                .then(response=>response.data)
                .then( data=>{
                        console.log(data)

                        let locations =  data.rows;

                        //F_COMPANIES
                        this.setState((prevState,props) => {
                            return {
                                ...prevState,
                                locations:[...locations],
                                counter:prevState.counter+1,
                            }
                        });
                    }
                )
                .catch(function (error) {
                    console.log(error);
                });
        },5000);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        //<div style={{width:'1850px',height:'860px',backgroundColor:'#2b2b2b',position:'relative',overflow:'hidden'}}>
        return (
            <div style={{width:'100%',height:'90%',backgroundColor:'#2b2b2b',position:'relative',overflow:'hidden'}}>
                <img src={zeekong} />
                {
                    this.state.locations.map((item,index)=>{
                        return (<div key={item.F_ID} className={'location'} style={{left:item.F_X+'px',top:item.F_Y+'px'}}><div>{item.F_TEXT}</div></div>);

                })
                }
                <Button variant="contained" color="primary" style={{position:'absolute',top:'50px',right:'50px'}} onClick={this.handleModalOpen}>
                    Primary
                </Button>
                <Modal
                    open={this.state.modelOpen}
                    onClose={this.handleModalClose}
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"

                >
                    <div style={{width:'700px',height:'500px',display:'flex',justifyContent:'center',alignItems:'center',backgroundColor:'#fff',margin:'100px auto',borderRadius:'10px',}}>
                        Modal
                    </div>
                </Modal>
            </div>
        );
    }
}
