import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';


import Carousel0 from '../images/Carousel0.jpg'
import Carousel1 from '../images/Carousel1.jpg'
import Carousel2 from '../images/Carousel2.jpg'
import Carousel3 from '../images/Carousel3.jpg'
import Carousel5 from '../images/Carousel5.jpg'
import Carousel6 from '../images/Carousel6.jpg'
import Carousel7 from '../images/Carousel7.jpg'


import {Divider, GridList, GridListTile, GridListTileBar, IconButton} from "@material-ui/core";
import {Home as HomeIcon,LocationOn as LocationOnIcon} from "@material-ui/icons";

export default class Welcome extends React.Component
{
    constructor(props)
    {
        super(props);




    }

    render() {

        let tiles=[
            {key:4,src:Carousel7,cols:2,rows:1},
            {key:5,src:Carousel5,cols:3,rows:1},
            {key:6,src:Carousel6,cols:1,rows:1},

            {key:1,src:Carousel0,cols:3,rows:2},
            {key:2,src:Carousel1,cols:2,rows:1},
            {key:3,src:Carousel2,cols:1,rows:1},

        ]

        return (

            <Container maxWidth={'xl'} style={{marginTop:30}} >
                <GridList style={{width:'100%',height:'100%',flexWrap:'wrap'}} cols={6} cellHeight={960} >

                    {tiles.map((tile,index)=>{
                        return (
                            <GridListTile rows={tile.rows} cols={tile.cols} key ={tile.key} style={{height:360}}>
                                <img src={tile.src} />
                                <GridListTileBar   actionIcon={
                                    <IconButton onClick={this.handleIconButtonClick}>
                                        <LocationOnIcon style={{color:'white'}}/>
                                    </IconButton>
                                }></GridListTileBar>
                            </GridListTile>


                        )

                    })}


                </GridList>
            </Container>
        )

    }
}
