


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import {
    Search as SearchIcon,
    Feedback as FeedbackIcon,
    EvStation as EvStationIcon,
    EventSharp as EventSharpIcon,
    Person as PersonIcon,
    AcUnit as AcUnitIcon,
    Save as SaveIcon,
    ArrowDropDownCircleOutlined as ArrowDropDownCircleOutlinedIcon,
    Details as DetailsIcon,
    DetailsOutlined as DetailsOutlinedIcon, PersonPin as PersonPinIcon,
    ArrowDropDownOutlined as ArrowDropDownOutlinedIcon,
    Widgets as WidgetsIcon,
    Explore as ExploreIcon,
    SaveAlt as SaveAltIcon,
} from '@material-ui/icons';

import {Button,Grid,Paper} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";
import {deepOrange} from "@material-ui/core/colors";


class MonthSearch extends React.Component {



    constructor(props)
    {
        super(props)
        this.state={

            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            companies:[{value:'01',text:'江苏大中'},{value:'02',text:'卧龙电驱'}],
            company:'01',

        };

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleCompanyChange=(e)=>{
            let company =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    company
                }
            })
        }

    }

    componentDidMount() {
        //F_ENERGY_ID, F_WORKUNIT_ID, F_YEAR, F_MONTH, F_WEEK, F_DAY

    }



    render() {

        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee'}}>
                <Grid container style={{marginTop:30}} spacing={3}>
                    <Grid item xs={12} sm={12}>

                        <ListItem >
                            <ListItemAvatar>
                                <Avatar style={{backgroundColor:deepOrange[500]}}>
                                    <PersonPinIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText>
                                <div style={{fontSize:24,color:'#0d47a1'}} className={'hwkt'}>电机月度数据查询</div>
                            </ListItemText>
                        </ListItem>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-search">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-search" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={1}>
                        <span style={{fontSize:22,lineHeight:2}} className={'hwkt'}>企业</span>
                    </Grid>

                    <Grid item xs={6} sm={3}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-company-search">
                                Company
                            </InputLabel>
                            <Select


                                labelWidth={80}
                                value={this.state.company}
                                onChange={this.handleCompanyChange}
                                input={<OutlinedInput name="Company" id="outlined-company-search" />}
                            >
                                {
                                    this.state.companies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>

                    <Grid item xs={6} sm={2}>
                        <Button variant="contained" color="primary">
                            <SearchIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>查询</span>

                        </Button>
                    </Grid>

                    <Grid item xs={6} sm={2}>
                        <Button variant="contained" color="secondary">
                            <SaveAltIcon />
                            <span style={{padding:5,fontSize:16}} className={'hwzs'}>下载</span>

                        </Button>
                    </Grid>
                </Grid>

                <Divider style={{marginBottom:30,marginTop:30}}/>



                <Paper>


                    <div style={{padding:30,fontSize:26,}}>
                        <Table >
                            <TableHead>
                                <TableRow style={{backgroundColor:'black',}}>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 年月</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 型号</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 功率kW</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 产量</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> 单价</span></TableCell>
                                    <TableCell ><span style={{color:'white',fontSize:20}} className={'hwkt'}> </span></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {[{key:1,month:'2019-01',model:'TC7112B14',quantity:820,price:780,power:230, },{key:2,month:'2019-02',model:'TC7112B14',quantity:932,price:780,power:230,  },{key:3,month:'2019-03',model:'TC7112B14',quantity:901,price:780,power:230,  },{key:4,month:'2019-04',model:'TC7112B14',quantity:934,price:780,power:230, }].map(row => (
                                    <TableRow key={row.key}>

                                        <TableCell ><span style={{fontSize:18}} className={'hwkt'}> {row.month} </span> </TableCell>
                                        <TableCell >{row.model}</TableCell>
                                        <TableCell >{row.power}</TableCell>
                                        <TableCell >{row.quantity}</TableCell>
                                        <TableCell >{row.price}</TableCell>
                                        <TableCell >
                                            <Button variant={'contained'} color={'primary'} onClick={this.download}>
                                                <ExploreIcon/>
                                                <span style={{padding:5,fontSize:16}} className={'hwzs'}>详细</span>
                                            </Button>

                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
                <Divider style={{marginBottom:30,marginTop:30}}/>
            </Container>
        );
    }
};

export default MonthSearch;

