
import React from 'react'
import { Layout, Menu, Icon } from 'antd';
import {Link} from "react-router-dom";

const { Header, Sider, Content} = Layout;

const { SubMenu } = Menu;

export default class MenuDemo extends React.Component
{
    state = {
        collapsed: false,
    };

    toggle = () => {
        this.setState({
            collapsed: !this.state.collapsed,
        });
    };
    render() {
        return(
            <Layout>
                <Sider trigger={null} collapsible collapsed={this.state.collapsed}>
                    <div className="logo" />
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={['1']} defaultOpenKeys={['sub1']}>

                        <SubMenu
                            key="sub1"
                            title={
                                <div >
                                    <Icon type="user" />
                                    <span className={'hwkt md-f'}>基本信息</span>
                                </div>
                            }
                        >
                            <Menu.Item key="1">
                                <Link to={"/"}><span className={'hwkt md-f'}>综合首页</span></Link>
                            </Menu.Item>
                            <Menu.Item key="2">
                                <Link to={"/MenuDemo"}>MenuDemo</Link>
                            </Menu.Item>
                            <Menu.Item key="3">option3</Menu.Item>
                            <Menu.Item key="4">option4</Menu.Item>
                        </SubMenu>
                        <SubMenu
                            key="sub2"
                            title={
                                <div>
                                    <Icon type="laptop" />
                                    <span>subnav 2</span>
                                </div>

                            }
                        >
                            <Menu.Item key="5">option5</Menu.Item>
                            <Menu.Item key="6">option6</Menu.Item>
                            <Menu.Item key="7">option7</Menu.Item>
                            <Menu.Item key="8">option8</Menu.Item>
                        </SubMenu>
                        <SubMenu
                            key="sub3"
                            title={
                                <div>
                                    <Icon type="notification" />
                                    <span>subnav 3</span>
                                </div>
                            }>
                            <Menu.Item key="9">option9</Menu.Item>
                            <Menu.Item key="10">option10</Menu.Item>
                            <Menu.Item key="11">option11</Menu.Item>
                            <Menu.Item key="12">option12</Menu.Item>
                        </SubMenu>
                    </Menu>
                </Sider>
                <Layout>
                    <Header style={{ background: '#fff', padding: 0 }}>
                        <Icon
                            style={{color:'black',margin:10}}
                            className="trigger"
                            type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                            onClick={this.toggle}
                        />
                    </Header>
                    <Content
                        style={{
                            margin: '24px 16px',
                            padding: 24,
                            background: '#fff',
                            minHeight: 280,
                        }}
                    >
                        Content
                    </Content>
                </Layout>
            </Layout>

        );
    }
}
