


import {connect} from "react-redux";
import SiderMenu from "./siderMenu";

import {action_setDefaultSelectedKeys,action_setDefaultOpenKeys,action_setDefaultKeys} from '../actions/menuAction'

const mapStateToProps= (state)=>{
    return ({
        defaultSelectedKeys : state.menu.defaultSelectedKeys,
        defaultOpenKeys: state.menu.defaultOpenKeys,

    });

}
const mapDispatchToProps=(dispatch)=>{
    return ({
        setDefaultKeys:(defaultKeys)=>dispatch(action_setDefaultKeys(defaultKeys))

    });
}

export default connect(mapStateToProps,mapDispatchToProps)(SiderMenu)
