import {connect} from "react-redux";
import Login from "./login";


import {action_validateSuccess,action_validateFail,action_setAccount,action_setAccountNo,action_setPassword,action_setCompanyID} from '../actions/userAction'
import {action_resetDefaultKeys} from '../actions/menuAction'


const mapStateToProps= (state)=>{
    return ({
        F_ACCOUNT_NO : state.user.F_ACCOUNT_NO,
        F_PASSWORD:state.user.F_PASSWORD,
        F_COMPANY_ID:state.user.F_COMPANY_ID,
        validated:state.user.validated,
    });

}



const mapDispatchToProps=(dispatch)=>{
    return ({
        validateSuccess:()=>dispatch(action_validateSuccess()),
        validateFail:()=>dispatch(action_validateFail()),
        setAccount:(F_ACCOUNT_NO,F_PASSWORD)=>dispatch(action_setAccount(F_ACCOUNT_NO,F_PASSWORD)),
        setAccountNo:(F_ACCOUNT_NO)=>dispatch(action_setAccountNo(F_ACCOUNT_NO)),
        setPassword:(F_PASSWORD)=>dispatch(action_setPassword(F_PASSWORD)),
        setCompanyID:(F_COMPANY_ID)=>dispatch(action_setCompanyID(F_COMPANY_ID)),
        resetDefaultKeys:()=>dispatch(action_resetDefaultKeys())

    });
}

export default connect(mapStateToProps,mapDispatchToProps)(Login)

