

const menuReducer = (state={defaultSelectedKeys:['1'],defaultOpenKeys:['sub1']},action)=>{

    switch (action.type) {
        case "setDefaultSelectedKeys":
            return {
                ...state,
                defaultSelectedKeys: action.payload,
            }
            break;
        case "setDefaultOpenKeys":
            return {
                ...state,
                defaultOpenKeys: action.payload,
            }
            break;

        case "setDefaultKeys":
            return {
                ...state,
                defaultSelectedKeys:action.payload.defaultSelectedKeys,

                defaultOpenKeys:action.payload.defaultOpenKeys,
            }
            break;

        case "resetDefaultKeys":
            console.info("resetDefaultKeys");
                return {
                    ...state,
                    ...action.payload
                }
                break;

        default:
            return state;
    }

}

export {menuReducer}
