

const defaultSelectedKeys = ['1'];
const defaultOpenKeys = ['sub1'];

function action_setDefaultSelectedKeys(defaultSelectedKeys) {
    return {
        type:'setDefaultSelectedKeys',
        payload:defaultSelectedKeys
    }
}

function action_setDefaultOpenKeys(defaultOpenKeys) {
    return {
        type:'setDefaultOpenKeys',
        payload:defaultOpenKeys
    }
}

function action_setDefaultKeys(defaultKeys) {
    return {
        type:'setDefaultKeys',
        payload:defaultKeys
    }
}

function action_resetDefaultKeys(defaultKeys) {
    return {
        type:'resetDefaultKeys',
        payload:{defaultSelectedKeys,defaultOpenKeys}
    }
}

export {action_setDefaultSelectedKeys,action_setDefaultOpenKeys,action_setDefaultKeys,action_resetDefaultKeys}