


import React from 'react';
import ReactEcharts from 'echarts-for-react';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';


import {Person  as PersonIcon ,AcUnit as AcUnitIcon,Save as SaveIcon} from '@material-ui/icons';

import {Button,Grid} from "@material-ui/core";
import axios from "axios";
import {server} from "../globals/global";
import {notification} from "antd";

import WorkunitRecudedConsumePieByPurpose from './WorkunitRecudedConsumePieByPurpose'

export default class EchartsFacade extends React.Component
{
    constructor(props)
    {
        super(props)

        this.getOption=()=>{
            return {
                title: {
                    text: '电流', x: '5px', y: '5px',
                    subtext: 'Current',
                    itemGap: 5,
                    //backgroundColor: '#588dd5',
                    padding: [10, 10, 10, 10],

                    textStyle: {
                        fontSize: 20, color: '#3398DB', fontFamily: '华文中宋',
                    },
                    subtextStyle: {
                        fontSize: 15, color: '#3398DB', fontFamily: 'Times New Roman'
                    },
                    show: true,
                },
                color: ['#3398DB'],
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                        type: 'shadow',        // 默认为直线，可选为：'line' | 'shadow'
                        //snap:true,
                    }
                },
                legend: {
                    data: [],
                    left: 120,
                    top: 15,
                    textStyle: {
                        fontSize: 18, color: '#3398DB', fontFamily: '华文中宋', textShadowBlur: 6,
                        textShadowColor: 'rgba(0,0, 0, 0.3)', textShadowOffsetX: 3, textShadowOffsetY: 3,
                    },
                    formatter: function (name) {
                        //return name + '(kwh)';
                        return name ;
                    },
                },
                grid: {
                    top: '15%',
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true,//grid 区域是否包含坐标轴的刻度标签。
                    show: true,
                    tooltip: {
                        trigger: 'axis',
                        textStyle: {
                            color: 'orange',
                        },
                    },
                },
                xAxis:  [{
                    type: 'category',
                    axisTick: {
                        alignWithLabel: true
                    },
                    scale: true,
                }],
                yAxis: [{
                    type: 'value',
                    splitArea: {show: true}
                }],
                series: []
            }
        }

        this.state={
            option:this.getOption()
        };

        this.handleButtonClick=(e=>{
            let params = new URLSearchParams()
            let F_ENERGY_ID = 'E01',F_WORKUNIT_ID='WU000001', F_YEAR= '2019', F_MONTH='7', F_WEEK='', F_DAY='8'

            params.append('entity', JSON.stringify({F_ENERGY_ID,F_WORKUNIT_ID,F_YEAR,F_MONTH,F_WEEK,F_DAY}))

            axios.post(server+ 'ECharts/TestEcharts',params )
                .then(response => response)
                .then(({ data }) => {

                    let option = this.getOption();

                    option.color = data.color;
                    option.legend.data = data.legend.data;
                    option.xAxis[0].data = data.xAxis.data;
                    option.series = data.series;

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })
                })
                .catch(function (error) {
                    console.log(error);
                });
        })
    }

    componentDidMount() {

        this.interval = setInterval(()=>{
            let params = new URLSearchParams()
            params.append('entity', JSON.stringify({}))
            axios.post(server+ 'ECharts/TestEcharts',params )
                .then(response => response)
                .then(({ data }) => {

                    let option = this.getOption();

                    option.color = data.color;
                    option.legend.data = data.legend.data;
                    option.xAxis[0].data = data.xAxis.data;
                    option.series = data.series;

                    this.setState((prevState,props)=>{
                        return {
                            ...prevState,
                            option
                        }
                    })
                })
                .catch(function (error) {
                    console.log(error);
                });
        },5000);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        return (
            <Container maxWidth={'xl'} style={{backgroundColor:'#eee'}}>
                <Grid container >
                    <Grid item sm={6}>
                        <Button variant={'contained'} style={{margin:'30px 10px',fontFamily:'华文中宋',fontSize:'20px'}} color={'primary'} onClick={this.handleButtonClick}>Search</Button>

                    </Grid>
                    <ReactEcharts
                        option={this.state.option}
                        style={{height: '550px', width: '1400px'}}
                        />

                </Grid>
            </Container>
        )
    }
}
