import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Tab,Tabs,ListItemText,ListItemIcon,MenuList,InputAdornment,TextField ,FormControlLabel,Switch,Tooltip ,GridListTileBar,GridList,GridListTile,Grid,Divider,Chip,Container,Menu,MenuItem,Fab,IconButton,Box,AppBar,Toolbar,Typography,Button,InputBase,Badge} from '@material-ui/core';

import {AccountCircle as AccountCircleIcon ,Home as HomeIcon,Face as FaceIcon ,AccessAlarm as AccessAlarmIcon,Menu as MenuIcon,Search as SearchIcon, Notifications  as NotificationsIcon, Add as AddIcon } from '@material-ui/icons';


import Carousel0 from '../images/Carousel0.jpg'
import Carousel1 from '../images/Carousel1.jpg'
import Carousel2 from '../images/Carousel2.jpg'
import Carousel3 from '../images/Carousel3.jpg'


export default  class MaterialUITabs extends  React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            value:0
        }

        this.handleChange=(e,value)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    value
                }
            })
        }
    }

    render() {
        //variant="fullWidth"
        return (
            <Box style={{height:'100vh'}}>
                <AppBar position={"static"}>
                    <Tabs value={this.state.value} onChange = {this.handleChange} variant={'scrollable'}>
                        <Tab value={0} label={'Home'} icon={<HomeIcon/>}></Tab>
                        <Tab value={1} label={'Face'} icon={<FaceIcon/>}></Tab>
                        <Tab value={2} label={'Account'} icon={<AccountCircleIcon/>}></Tab>
                        <Tab value={3} label={'AccessAlarm'} icon={<AccessAlarmIcon/>}></Tab>
                        <Tab value={4} label={'Menu'} icon={<MenuIcon/>}></Tab>
                        <Tab value={5} label={'Search'} icon={<SearchIcon/>}></Tab>
                        <Tab value={6} label={'Notifications'} icon={<NotificationsIcon/>}></Tab>
                        <Tab value={7} label={'Add'} icon={<AddIcon/>}></Tab>

                    </Tabs>
                </AppBar>
                {this.state.value == 0 && <Typography style={{padding:20}} variant={'h6'}>Home</Typography> }
                {this.state.value == 1 && <Typography style={{padding:20}} variant={'h6'}>Face</Typography> }
            </Box>

        )
    }
}
