
import React from 'react'

import {Table} from 'antd'

import energyTypeEntity from '../entities/energyTypeEntity'

import Carousel0 from '../images/Carousel0.jpg'

export default class HomePage extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state={
            columns:[],
            dataSource : [],
        }
    }

    columns(){
        const columns = [
            {
                title: '时间',
                dataIndex: 'F_TIME_TYPE',
                key: 'F_TIME_TYPE',
            },
            {
                title: '综合能耗',
                dataIndex: 'F_REDUCED_CONSUME',
                key: 'F_REDUCED_CONSUME',
            },
            {
                title: '能源成本',
                dataIndex: 'F_ENERGY_COST',
                key: 'F_ENERGY_COST',
            },
        ];



        return columns;
    }


    componentDidMount() {
        let columns = this.columns()
        let prefix ='F_ENERGY_CONSUME_';
        energyTypeEntity.getEnergyType().then((response)=>{

            response.data.forEach((item)=>{
                columns.push(
                    {
                        title: '耗'+ item.F_ENERGY_NAME +'量('+item.F_UNIFY_UNIT+')',
                        dataIndex: prefix+item.F_ENERGY_ID,
                        key: prefix+item.F_ENERGY_ID,
                    }
                )
            });

            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    columns,
                }

            });
            return energyTypeEntity.enterpriseHomePage();
        }).then((response)=>{

            this.setState((prevState,props)=>{
                return{
                    ...prevState,
                    dataSource : response.data.rows,
                }

            });


        })
            .catch((error)=>
            {
                console.info(error)
            }
        )
    }

    render() {
        return (
            /*
            <Table size="small" dataSource={this.state.dataSource} columns={this.state.columns} bordered={false} pagination={false} scroll={ {x:true} } style={{marginBottom:20}}/>
             */
            <div style={{overflow:'auto',color:'#282828',flexDirection:'column'}}>

                <div style={{overflow:'auto',marginBottom:15}}>
                <table style={{width:'100%',marginBottom:10}}>
                    <thead>
                        <tr style={{backgroundColor:'#111',color:'white',fontSize:18}}>

                            {this.state.columns.map((item,index)=>{
                                return (<td style={{padding:10,fontFamily:'hwkt',textAlign:'right'}} key={item.key}>{item.title}</td >);

                            })}
                        </tr>
                    </thead>
                    <tbody>

                    {
                        this.state.dataSource.map((row,index)=>{
                            return (<tr key={row.F_TIME_TYPE} style={{borderBottom:'1px solid #ccc' }} >

                                {this.state.columns.map((column,index)=>{
                                    return (<td key={column.dataIndex} style={{textAlign:'right',padding:10,fontFamily:'hwkt',fontSize:20}}>{row[column.dataIndex]}</td>)
                                })}

                            </tr>)
                        })
                    }

                    </tbody>

                </table>
                </div>
                <img src={Carousel0} style={{width:'100%'}}/>


            </div>
            )
    }
}
