import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Grid,Radio,RadioGroup,FormHelperText,FormControlLabel,FormControl ,FormLabel}from '@material-ui/core';



export default class RadioButtonsGroup extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state={
            gender:'male'
        }
        this.handleChange=(e)=>{
            let gender = e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    gender
                };
            });
            console.info(this.state);
        }
    }

    render() {

        return (
            <FormControl component="fieldset" >


                        <FormLabel component="label" ></FormLabel>


                        <RadioGroup
                            aria-label="Gender"
                            name="gender"
                            value={this.state.gender}
                            onChange={this.handleChange}
                            row
                        >
                            <FormControlLabel value="female" control={<Radio />} label="Female" />
                            <FormControlLabel value="male" control={<Radio />} label="Male" />

                        </RadioGroup>




            </FormControl>

        );
    }

}
