import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Grid,Divider,Chip,Container,Menu,MenuItem,Fab,IconButton,Box,AppBar,Toolbar,Typography,Button,InputBase,Badge} from '@material-ui/core';

import {Face as FaceIcon ,AccessAlarm,Menu as MenuIcon,Search as SearchIcon, Notifications  as NotificationsIcon, Add as AddIcon } from '@material-ui/icons';

import '../css/materialUIDemo.css'

export default class MaterialUIAppBar extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state={
            anchorElement:null,
            open:false,
        }

        this.handleIconButtonClick=(e)=>{
            let {open} = this.state
            let anchorElement = e.currentTarget
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    open:!open,
                    anchorElement
                }
            })
        };
        this.handleMenuItemClick=(e)=>{
            let {open} = this.state
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    open:!open,
                    anchorElement:null,
                }
            })
        }
    }

    render() {
        return (
            <div>

                <Container style={{height:'50vh'}}>

                    <Box style={{display:'flex',justifyContent:'center',alignContent:'center',margin:20,}}>
                        <Chip variant="outlined"  icon={<FaceIcon />} label="Simple App Bar" style={{fontSize:20}}/>
                    </Box>

                        <AppBar  position="static" color={"default"}>
                            <Toolbar>
                                <Typography variant="h6" color="inherit">
                                    Photos
                                </Typography>
                            </Toolbar>
                        </AppBar>

                    <Box style={{margin:20}}>
                        <Typography variant="h6" color="inherit">
                            AppBar--ToolBar--Typography
                        </Typography>

                    </Box>


                </Container>
                <Container style={{height:'50vh'}}>

                    <Box style={{display:'flex',justifyContent:'center',alignContent:'center',margin:20,}}>
                        <Chip variant="outlined"  icon={<FaceIcon />} label="App Bar with buttons" style={{fontSize:20}}/>

                    </Box>
                        <AppBar  position="static">
                            <Toolbar>
                                <IconButton color={'inherit'}>
                                    <MenuIcon/>
                                </IconButton>
                                <Typography variant={'h6'} color={'inherit'} style={{flexGrow:1}}>
                                    News
                                </Typography>
                                <Button color={'inherit'} >Login</Button>
                            </Toolbar>
                        </AppBar>
                    <Grid container>
                        <Grid Item>
                            <pre>
                                <code>
                                AppBar--Toolbar+-IconButton--Icon
                                               +-Typography
                                               +-Button
                                </code>
                            </pre>

                        </Grid>
                        <Grid Item>

                        </Grid>
                        <Grid Item>

                        </Grid>

                    </Grid>

                </Container>
                <Container style={{width:'300vh'}}>

                <AppBar  position="static"  >
                <Toolbar style={{justifyContent:'space-between'}}>
                    <IconButton  edge="start"  color="inherit" aria-label="Menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" color="inherit" style={{marginRight:20,flexGrow:1}}>
                        Photos
                    </Typography>

                    <Box style={{backgroundColor:'#82b1ff',borderRadius:5,marginRight:100,display:'flex'}}>
                    <InputBase
                        className={'searchBar'}
                        placeholder="Search…"
                      inputProps={{ 'aria-label': 'Search' }}
                        style={{color:'white',paddingLeft:20}}
                    />
                        <IconButton color="inherit">
                            <SearchIcon/>
                        </IconButton>

                    </Box>


                    <IconButton color={'inherit'} onClick={this.handleIconButtonClick}>
                        <Badge badgeContent={'2'} color={'secondary'}>
                            <NotificationsIcon/>
                        </Badge>

                    </IconButton>

                    <Menu
                        id="menu-appbar"
                        anchorEl={this.state.anchorElement}
                        keepMounted
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        transformOrigin={{
                            vertical: -50,
                            horizontal: 'center',
                        }}
                        open={this.state.open}
                    >
                        <MenuItem onClick={this.handleMenuItemClick}>Profile</MenuItem>
                        <MenuItem onClick={this.handleMenuItemClick}>Profile</MenuItem>
                        <MenuItem onClick={this.handleMenuItemClick}>Profile hello world</MenuItem>
                    </Menu>

                    <Button color="inherit">Login</Button>
                    <Fab color='secondary' style={{position:'absolute',zIndex:1,top:-30,marginLeft:'auto',marginRight:'auto',left:0,right:0}}>
                        <AddIcon/>
                    </Fab>

                </Toolbar>

            </AppBar>
                </Container>

            </div>

            )


    }
}


