import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Divider from '@material-ui/core/Divider';

import {
    Person as PersonIcon,
    AcUnit as AcUnitIcon,
    Save as SaveIcon,
    PersonPin as PersonPinIcon
} from '@material-ui/icons';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import ListItemText from '@material-ui/core/ListItemText';



import Button from "@material-ui/core/Button";

import {deepOrange} from "@material-ui/core/colors";


export default class MonthInput extends React.Component
{
    getLoads=()=>
    {
        return [
            {value:1,text:'风机'},
            {value:2,text:'水泵'},
            {value:3,text:'压缩机'},
            {value:4,text:'其它设备'},

        ];

    };

    getEfficiencies=(machineryType)=>
    {
        if (machineryType==3)
        {
            return [
                {value:4,text:'三级能效'},
                {value:5,text:'二级能效'},
                {value:6,text:'一级能效'},
            ];

        }
        if (machineryType==4)
        {
            return [
                {value:0,text:''},

            ];

        }
        return [
            {value:1,text:'IE2'},
            {value:2,text:'IE3'},
            {value:3,text:'IE4'},

        ];

    };
    getVoltages=(machineryType)=>
    {

        if (machineryType==3)
        {
            return [
                {value:4,text:'6KV'},
                {value:5,text:'10KV'},
            ];

        }
        if (machineryType==4)
        {
            return [
                {value:0,text:''},

            ];

        }
        return [
            {value:1,text:'380'},
            {value:2,text:'660'},
            {value:3,text:'其它'},

        ];

    }

    constructor(props)
    {
        super(props);




        let machineryType =1;
        let load = 1;
        let voltages = this.getVoltages(machineryType);
        let voltage= voltages[0].value

        let efficiencies =this.getEfficiencies(machineryType);
        let efficiency= efficiencies[0].value

        let year = 2019

        this.state = {
            machineryTypes:[{value:1,text:'低压高效'},{value:2,text:'高效永磁'},{value:3,text:'高压高效'},{value:4,text:'其它'},],
            machineryType,
            voltages,
            voltage,
            efficiencies,
            efficiency,
            loads:[{value:1,text:'风机'},{value:2,text:'水泵'},{value:3,text:'高压高效'},{value:4,text:'其它'},],
            load,
            years:[{value:2019,text:'2019'},{value:2020,text:'2020'},{value:2021,text:'2021'},{value:2022,text:'2022'},{value:2023,text:'2023'},],
            year:2019,
            months:[{value:1,text:'1'},{value:2,text:'2'},{value:3,text:'3'},{value:4,text:'4'},{value:5,text:'5'},{value:6,text:'6'},{value:7,text:'7'},{value:8,text:'8'},{value:9,text:'9'},{value:10,text:'10'},{value:11,text:'11'},{value:12,text:'12'},],
            month:1

    }

        console.info(this.state)

        this.handleMachineryTypeChange=(e)=>{
            let machineryType =  e.target.value
            let voltages = this.getVoltages(machineryType);
            let voltage= voltages[0].value
            let efficiencies =this.getEfficiencies(machineryType);
            let efficiency= efficiencies[0].value

            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    machineryType,
                    voltages,
                    voltage,
                    efficiencies,
                    efficiency
                }
            })
        };
        this.handleVoltageChange=(e)=>{
            let voltage =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    voltage
                }
            })
        };
        this.handleEfficiencyChange=(e)=>{
            let efficiency =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    efficiency
                }
            })
        }

        this.handleLoadChange=(e)=>{
            let load =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    load
                }
            })
        }

        this.handleYearChange=(e)=>{
            let year =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    year
                }
            })
        }

        this.handleMonthChange=(e)=>{
            let month =  e.target.value
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    month
                }
            })
        }
    }

    render() {


        return (
            <Container maxWidth={'md'} style={{backgroundColor:'#eee',marginTop:30}}>


                <div >
                    <List>

                    <ListItem >
                        <ListItemAvatar>
                            <Avatar style={{backgroundColor:deepOrange[500]}}>
                                <PersonPinIcon />
                            </Avatar>
                        </ListItemAvatar>
                        <ListItemText>
                            <div style={{fontSize:26,marginTop:20,marginBottom:20,color:deepOrange[500]}}  className={'hwkt'}>
                                <div>电机月度数据录入</div>

                            </div>
                        </ListItemText>
                    </ListItem>
                    </List>
                </div>


                <Divider style={{marginBottom:20}}/>

                <Grid container spacing={3}>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>型号规格</span>
                    </Grid>

                    <Grid item xs={8} sm={4}>
                        <TextField
                            required
                            id="Specification"
                            name="Specification"
                            label="Specification"
                            fullWidth
                            autoComplete="Specification"
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>电机类型</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-machineryType-simple">
                                Machinery
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.machineryType}
                                onChange={this.handleMachineryTypeChange}
                                input={<OutlinedInput name="MachineryType" id="outlined-machineryType-simple" />}
                            >
                                {
                                    this.state.machineryTypes.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>电压类型</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>
                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-voltage-simple">
                                Voltage
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.voltage}
                                onChange={this.handleVoltageChange}
                                input={<OutlinedInput name="Voltage" id="outlined-voltage-simple" />}
                            >


                                {
                                    this.state.voltages.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>能效等级</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>
                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-efficiency-simple">
                                Efficiency
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.efficiency}
                                onChange={this.handleEfficiencyChange}
                                input={<OutlinedInput name="Efficiency" id="outlined-efficiency-simple" />}
                            >


                                {
                                    this.state.efficiencies.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }

                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>功率kW</span>
                    </Grid>

                    <Grid item xs={8} sm={4}>
                        <TextField
                            required
                            id="Power"
                            name="Power"
                            label="Power"
                            fullWidth
                            autoComplete="Power"
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>数量</span>
                    </Grid>

                    <Grid item xs={8} sm={4}>
                        <TextField
                            required
                            id="Quantity"
                            name="Quantity"
                            label="Quantity"
                            fullWidth
                            autoComplete="Quantity"
                            variant="outlined"
                        />
                    </Grid>

                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>负载类型</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-load-simple">
                                Load
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.load}
                                onChange={this.handleLoadChange}
                                input={<OutlinedInput name="Load" id="outlined-load-simple" />}
                            >
                                {
                                    this.state.loads.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>年份</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-year-simple">
                                Year
                            </InputLabel>
                            <Select
                                labelWidth={80}
                                value={this.state.year}
                                onChange={this.handleYearChange}
                                input={<OutlinedInput name="Year" id="outlined-year-simple" />}
                            >
                                {
                                    this.state.years.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>月份</span>
                    </Grid>
                    <Grid item xs={8} sm={4}>

                        <FormControl variant="outlined" fullWidth>
                            <InputLabel htmlFor="outlined-month-simple">
                                Month
                            </InputLabel>
                            <Select
                                MenuProps={{PaperProps: {
                                        style: {
                                            maxHeight: 48* 4.5,

                                        },
                                    }}}
                                labelWidth={80}
                                value={this.state.month}
                                onChange={this.handleMonthChange}
                                input={<OutlinedInput name="Month" id="outlined-month-simple" />}
                            >
                                {
                                    this.state.months.map((item)=>{
                                        return (<MenuItem value={item.value} key={item.value}>{item.text}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={4} sm={2}>
                        <span style={{fontSize:26,lineHeight:2}} className={'hwkt'}>单价</span>
                    </Grid>

                    <Grid item xs={8} sm={4}>
                        <TextField
                            required
                            id="Price"
                            name="Price"
                            label="Price"
                            fullWidth
                            autoComplete="Price"
                            variant="outlined"
                        />
                    </Grid>
                    <Grid item xs={10} sm={10}>

                    </Grid>
                    <Grid item xs={2} sm={2}>
                        <Button variant="contained" color="primary" >
                            <SaveIcon />
                            <span style={{padding:10,fontSize:20}} className={'hwzs'}>保存</span>

                        </Button>
                    </Grid>

                </Grid>
            </Container>

        )
    }
}

