import {Input} from "antd/lib";

import React from "react";
import axios from "axios";

import {Button, Carousel, Icon, Layout, notification, Tooltip,Select} from "antd";

import Carousel0 from "../images/Carousel0.jpg";
import Carousel2 from "../images/Carousel2.jpg";
import Carousel3 from "../images/Carousel3.jpg";

import {server} from '../globals/global'

const { Header, Content } = Layout;

export default class Login extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state={
            //F_ACCOUNT_NO : '',
            //F_PASSWORD:'',
            F_COMPANIES:[
                //{F_COMPANY_ID:"",F_COMPANY_NAME:"空白"},
                //{F_COMPANY_ID:"1",F_COMPANY_NAME:"江苏大中"},
                //{F_COMPANY_ID:"2",F_COMPANY_NAME:"卧龙电驱"}
            ]
        };

        this.handleCompanyChange= (value)=>{


            this.props.setCompanyID(value);


        }

        this.handleAccountChange= (event)=>{

            let F_ACCOUNT_NO = event.target.value;
            this.props.setAccountNo(F_ACCOUNT_NO);


        }

        this.handlePasswordChange= (event)=>{


            let F_PASSWORD= event.target.value;
            this.props.setPassword(F_PASSWORD);
        };

        this.handleLogin=(event)=>{

            let {validateSuccess,validateFail,setAccount} = this.props;

            //validateSuccess();
            //return;

            let {F_ACCOUNT_NO,F_PASSWORD,F_COMPANY_ID} = this.props;
            let params = new URLSearchParams()
            params.append('entity', JSON.stringify({F_ACCOUNT_NO,F_PASSWORD,F_COMPANY_ID}))

            axios.post(server+ 'Login/VerifyUser',params )
                .then(function (response) {
                    //setAccount(F_ACCOUNT_NO,F_PASSWORD);
                    let {icon,msg} = response.data
                    if (icon === "error") {
                        notification['error']({
                            message: msg,
                            description:
                                '请确认[企业][用户名][密码]',
                            placement: 'bottomRight',
                            bottom: 50,
                            duration: 2,
                        });
                        validateFail();
                    }
                    else {
                        notification['success']({
                            message: msg,
                            description:
                                '欢迎光临',
                            placement: 'bottomRight',
                            bottom: 50,
                            duration: 1,
                        });
                        //$('#frmLogin').submit();
                        validateSuccess();
                        return axios.post(server+ 'Login/Login',params )
                    }
                })
                .then(function (response) {


                })
                .catch(function (error) {
                    console.log(error);
                });

            return;
            /*
            //axios get demo
            axios({
                method:'get',
                url:server + 'Login/Verify',
                headers: { 'content-type': 'application/x-www-form-urlencoded' },
                params: {
                    entity:JSON.stringify({F_ACCOUNT_NO:'admin',F_PASSWORD:'admin'})
                }
            })
                .then(function(response){
                    console.log(response);
                })
                .catch(function(error){
                    console.log(error);
                });
                */
        };
    }


    componentDidMount() {

        let params = new URLSearchParams()
        params.append('entity', JSON.stringify({}))
        axios.post(server+ 'Company/GetAllCompany',params )
            .then(response=>response.data)
            .then( data=>{
                    //console.info(data)
                    let F_COMPANIES =  data.rows;


                    //F_COMPANIES
                    this.setState((prevState,props) => {
                        return {
                            ...prevState,
                            F_COMPANIES
                        }
                    });

                    if (F_COMPANIES.length)
                    {
                        this.props.setCompanyID(F_COMPANIES[0].F_COMPANY_ID);
                    }
                }
            )
            .catch(function (error) {
                console.log(error);
            });

    }

    render() {


            return(


                    <Layout>
                        <Header  style={{position:'fixed', zIndex: 1, width: '98%', }}>


                            <div style={{display:'flex',paddingTop:18,justifyContent:'flex-end'}}>

                                <Select
                                    placeholder="选择企业"
                                    style={{ width: 200,marginRight:10}}
                                    onChange={this.handleCompanyChange} value={this.props.F_COMPANY_ID}
                                >
                                    {this.state.F_COMPANIES.map(item => (
                                        <Select.Option key={item.F_COMPANY_ID} value={item.F_COMPANY_ID}>
                                            {item.F_COMPANY_NAME}
                                        </Select.Option>
                                    ))}
                                </Select>
                                <Input style={{width:200,marginRight:10}}
                                       placeholder="账号"
                                       prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                       suffix={
                                           <Tooltip title="输入账号">
                                               <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                                           </Tooltip>
                                       }
                                       onChange={this.handleAccountChange} value={this.props.F_ACCOUNT_NO}
                                />

                                <Input.Password style={{width:200,marginRight:10}}
                                                placeholder="密码"
                                                prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                                                suffix={
                                                    <Tooltip title="输入密码">
                                                        <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                                                    </Tooltip>
                                                }
                                                onChange={this.handlePasswordChange} value={this.props.F_PASSWORD}
                                />
                                <Button type="primary" shape="round" icon="login" onClick={this.handleLogin}>
                                    Login
                                </Button>
                            </div>


                        </Header>
                        <Content >

                            <div style={{width:'98%',backgroundColor:'#222',overflow:'hidden'}}>

                                <div>
                                    <Carousel autoplay >
                                        <div style={{display:'flex',alignItems:'center'}}>
                                            <img src={Carousel0} style={{width:'90%',marginLeft:'auto',marginRight:'auto'}} alt={'Energy'}/>
                                        </div>

                                        <div>
                                            <img src={Carousel2} style={{width:'90%',marginLeft:'auto',marginRight:'auto'}} alt={'Energy'}/>
                                        </div>
                                        <div>
                                            <img src={Carousel3} style={{width:'90%',marginLeft:'auto',marginRight:'auto'}} alt={'Energy'}/>
                                        </div>
                                    </Carousel>
                                </div>
                                <div className={'title'} style={{padding:10,backgroundColor:'rgba(0,0,0,0.3)',width:'87%'}}>
                                    {/*
                                    <div style={{marginBottom:0,fontSize:'3rem',textAlign:'left',}} className={'hwzs'}>全国高效电机市场监控系统</div>
                                    <div style={{marginBottom:0,fontSize:'1.2rem',textAlign:'left',fontFamily:'Vipp Neue',fontWeight:'bold'}}>National High Efficiency Motor Market Monitoring System</div>
                                    <div style={{fontSize:'1.5rem',fontFamily:'Vipp Neue',textAlign:'left',display:'none'}}>ShangHai Electric Apparatus Research Institute</div>
                                    */}
                                    <div style={{marginBottom:0,fontSize:'3rem',textAlign:'left',}} className={'hwzs'}>展厅水泵演示系统</div>
                                    <div style={{marginBottom:0,fontSize:'1.2rem',textAlign:'left',fontFamily:'Helvitica',fontWeight:'bold'}}>ShangHai Electric Apparatus Research Institute</div>

                                </div>
                            </div>
                        </Content>
                    </Layout>


            )


    }
}
