import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {DialogActions,LinearProgress ,Dialog,DialogTitle ,Modal,CircularProgress,Tab,Tabs,ListItemText,ListItemIcon,MenuList,InputAdornment,TextField ,FormControlLabel,Switch,Tooltip ,GridListTileBar,GridList,GridListTile,Grid,Divider,Chip,Container,Menu,MenuItem,Fab,IconButton,Box,AppBar,Toolbar,Typography,Button,InputBase,Badge} from '@material-ui/core';

import {Done as DoneIcon ,Close as CloseIcon,Save as SaveIcon,AccountCircle as AccountCircleIcon ,Home as HomeIcon,Face as FaceIcon ,AccessAlarm as AccessAlarmIcon,Menu as MenuIcon,Search as SearchIcon, Notifications  as NotificationsIcon, Add as AddIcon } from '@material-ui/icons';


import Carousel0 from '../images/Carousel0.jpg'
import Carousel1 from '../images/Carousel1.jpg'
import Carousel2 from '../images/Carousel2.jpg'
import Carousel3 from '../images/Carousel3.jpg'

export default class MaterialUIProgress extends React.Component
{

    constructor(props)
    {
        super(props);
        this.state ={
            open:false
        }

        this.handleClose=(e)=>{
            this.setState((prevState,porps)=>{
                return {
                    ...prevState,
                    open:false,

                };
            })
        }

        this.handleOpen=(e)=>{
            this.setState((prevState,porps)=>{
                return {
                    ...prevState,
                    open:true,

                };
            })
        }
    }

    render() {
        return (
            <Box style={{height:'100vh'}}>


                <AppBar position={'static'} color={'default'}>
                    <Box m={4}>
                        <IconButton
                            aria-label="Save"
                            color="primary"
                            onClick={this.handleOpen}>
                            <SaveIcon />
                        </IconButton>

                        <Dialog open={this.state.open} >
                            <DialogTitle id="simple-dialog-title"  >
                                <AccountCircleIcon/>
                            </DialogTitle>

                            <Grid container style={{width:500,padding:20}}>
                                <Grid item xs={2}/>
                                <Grid item xs={2}>
                                    <CircularProgress color={'secondary'} />
                                </Grid>
                                <Grid item xs={1}/>
                                <Grid item xs={5}>
                                    <Typography variant={'h6'}>Loading...</Typography>
                                </Grid>
                            </Grid>
                            <DialogActions >
                                <IconButton>
                                    <DoneIcon/>
                                </IconButton>
                                <IconButton onClick={this.handleClose}>
                                    <CloseIcon/>
                                </IconButton>
                            </DialogActions>
                        </Dialog>
                    </Box>
                </AppBar>
            </Box>
        )
    }
}
